$desktopPadding: 60px;
$BREAKPOINT_ROW: 800px;
.ShowroomPopIn {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 13;
	height: 100%;
	width: 100vw;
	opacity: 0;
	visibility: hidden;
	overflow-y: scroll;
	padding-left: 20px;
	padding-right: 20px;
	padding-bottom: 50px;
	padding-top: 80px;
	overscroll-behavior: contain;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	flex-flow: column;
	contain: strict;
	background-image: radial-gradient(transparent 50%, rgba(0, 0, 0, 0.85) 150%), url('/assets/images/menu_noise.png'), linear-gradient(-55deg, #122330 15%, #431e2e 100%);
	@include bp(mds) {
		//justify-content: center;
		padding-left: $desktopPadding;
		padding-right: $desktopPadding;
		padding-bottom: $desktopPadding;
		padding-top: $desktopPadding;
		//background-image: none;
	}

	.closeLayerButton {
		top: 40px;
		right: 40px;
		@include bp(md) {
			top: 80px;
			right: 80px;
		}
	}

	.PopInContent {
		@include bp(mds) {
			margin: auto;
		}
		display: flex;
		flex-direction: column-reverse;
		width: 100%;
		//min-height: 100%;
		padding: 0;
		@media (min-aspect-ratio: 1/1) and (max-height: 500px) {
			//align-items: center;
			flex-direction: row;
			height: 100%;
			max-height: 100%;
		}
		@media (min-width: $BREAKPOINT_ROW) {
			//align-items: center;
			flex-direction: row;
			height: 100%;
			max-height: 100%;
		}
		@media (min-width: $BREAKPOINT_LANDING_TABLET_LANDSCAPE) {
			//padding: 20px 0 20px 0;
		}
		@media (min-width: $BREAKPOINT_LANDING_TABLET_LANDSCAPE) and (min-height: 960px) {
			padding: 100px 0 50px 0;
		}
	}

	.ItemAbout {
		max-width: 420px;
		flex-shrink: 0;
		margin-top: 50px;
		@media (min-aspect-ratio: 1/1) and (max-height: 500px) {
			max-width: 260px;
		}

		@media (min-height: 501px) and (max-width: $BREAKPOINT_ROW - 1px) {
			margin-left: auto;
			margin-right: auto;
		}
		@media (min-aspect-ratio: 1/1) and (max-height: 500px) {
			margin-top: auto;
			margin-bottom: auto;
		}
		@media (min-width: $BREAKPOINT_ROW) {
			margin-top: auto;
			margin-bottom: auto;
			max-width: 260px;
		}
	}

	.ItemTitle {
		font-size: 24px;
		line-height: em(32px, 24px);
		color: $COLOR_WHITE;
		font-family: $font-roobert;
		font-weight: $font-weight-roobert-light;
		margin-bottom: 24px;
	}

	.ItemDescription {
		p {
			@extend .text-14;
			color: rgba($COLOR_WHITE, 0.56);
			margin-bottom: 1em;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	.ItemInfos {
		margin-bottom: 12px;

		p {
			padding: 13px 0;
			@extend .dotted-border;
			color: rgba($COLOR_WHITE, 0.56);
		}
	}

	.ItemCtas {
		a {
			@extend .arrow-hover-left;
			@extend .text-14;
			padding-bottom: 13px;
			padding-top: 13px;
			color: $COLOR_WHITE;
			border-bottom: 1px solid rgba($COLOR_WHITE, 0.56);
			margin-bottom: 12px;
			display: flex;
			align-items: center;
			transition: 0.2s;

			&:hover {
				border-color: rgba($COLOR_WHITE, 1);
			}

			.SecondaryText {
				margin-left: 6px;
				color: rgba($COLOR_WHITE, 0.56);

				&:before {
					content: '';
					margin-right: 6px;
				}
			}
		}
	}

	&.iframe-mode {
		@media (min-aspect-ratio: 1/1) and (max-height: 500px) {
			.ItemMedia {
				position: fixed;
				top: 0;
				left: 300px;
				right: 0;
				width: auto;
				height: 100vh;

				.MediaWrapper {
					max-width: none;
				}
			}
		}
		@media (min-width: $BREAKPOINT_ROW) and (min-height: 501px) {
			.ItemMedia {
				position: fixed;
				top: 0;
				left: 300px;
				right: 0;
				width: auto;
				height: 100vh;

				.MediaWrapper {
					max-width: none;
				}
			}
		}
		@include bp(mds) {
			.ItemMedia {
				left: 260px + $desktopPadding + 20px;
			}
		}

		@media (max-aspect-ratio: 1/0.99999) and (max-width: $BREAKPOINT_ROW - 1px) {
			padding-left: 0px;
			padding-right: 0px;

			.ItemAbout {
				padding-left: 20px;
				padding-right: 20px;
			}
		}
	}
	&:not(.iframe-mode) {
		.ItemMedia {
			&:before {
				position: absolute;
				left: 0;
				right: 0;
				text-align: center;
				top: calc(50% - 10px);
				content: 'Loading...';
				color: rgba(255, 255, 255, 0.56);
				z-index: -1;
				font-family: $font-rogan;
				font-weight: $font-weight-rogan-bold;
				font-size: 13px;
				letter-spacing: 3px;
				text-transform: uppercase;
			}
		}
	}

	.ItemMedia {
		width: 100%;
		//max-width: 600px;
		//height: 300px;
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		height: 100%;

		min-height: 300px;

		@include bp(md) {
			margin: 0;
		}

		iframe,
		.SketchFabEmbed {
			width: 100%;
			height: 100%;
		}

		.MediaWrapper {
			position: relative;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			opacity: 0;
			transition: opacity 0.5s;

			&.visible {
				opacity: 1;
			}

			@include bp(mds) {
				max-width: 70vw;
			}
		}

		.ImageEmbed {
			width: 100%;
			height: 100%;

			.imageContainer {
				width: 100%;
				height: 100%;
			}

			.imageInner {
				width: 100%;
				height: 100%;
				img {
					width: 100%;
					height: 100%;
					object-fit: contain;
					object-position: center;
					position: static !important;
				}
			}
		}
	}
}

.ShowroomPopIn-CoverLayer {
	position: fixed;
	top: 0;
	left: 0;
	display: block;
	width: 100vw;
	height: 100vh;
	will-change: transform;
	background-color: $COLOR_BEIGE;
	z-index: 0;
	pointer-events: none;
	opacity: 1;
	transition: opacity 0.5s;
	transform: translate3d(0, 0, 0);
	contain: strict;
}

.ShowroomPopIn-Background {
	position: fixed;
	display: block;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	pointer-events: none;
	will-change: transform;
	transform-origin: center top;
	transform: rotateZ(0.001deg); //works around a chrome bug, if removed Chrome uses an excessive amount of GPU memory when animating scale
	z-index: 12;
	opacity: 0;
	contain: strict;
}

.ShowroomPopIn-Background-overlay {
	position: fixed;
	top: calc(-120vw * 0.5);
	left: calc(50% - 120vw * 0.5);
	width: 120vw;
	height: 120vw;
	@include bp(md) {
		top: calc(-37vw * 0.52);
		left: calc(50% - 37vw * 0.5);
		width: 37vw;
		height: 37vw;
	}
	display: block;
	will-change: transform;
	//transform: translateX(-50%) translateY(-50%);
	//animation: rotateForeverReverse 100s linear infinite;
	z-index: 15;
	transform-origin: center center;
	transform: rotateZ(0.001deg);
	contain: strict;

	&:after {
		content: '';
		position: absolute;
		width: calc(52% - 2vw);
		height: calc(52% - 2vw);
		border-radius: 50%;
		display: block;
		top: calc((100% - 52%) * 0.5 + 1vw);
		left: calc((100% - 52%) * 0.5 + 1vw);
		box-shadow: $COLOR_BEIGE 1vw 0.1vw 1vw 1vw, $COLOR_BEIGE -1vw 0.1vw 1vw 1vw;
		background: $COLOR_BEIGE;
		z-index: -1;
	}

	img {
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		opacity: 1;
		width: 100%;
		height: 100%;
	}

	pointer-events: none;
	opacity: 0;
}
