.MarkdownModule {
	.moduleInner {
		min-height: 100vh;
		padding-bottom: 130px;
		padding-top: 120px;

		@include bp(sm) {
			padding-top: 200px;
			padding-bottom: 200px;
		}

		@include bp(md) {
			padding-top: 200px;
			padding-bottom: 200px;
		}
	}

	h1 {
		margin-bottom: 56px;
	}

	h2 {
		margin-top: 16px;

		@include bp(sm) {
			margin-bottom: 20px;
			margin-top: 40px;
		}
	}

	h3 {
		margin-bottom: 16px;
		margin-top: 36px;
	}

	p {
		@extend .text-16;
	}

	p {
		margin-bottom: 20px;

		&:last-of-type {
			margin-bottom: 0;
		}
	}

	h3 {
		color: #2f2425;
		font-size: 22px;
		line-height: 1.2em;
	}

	ol {
		list-style: none;
		margin-bottom: 20px;
		counter-reset: ordered-list;

		li {
			counter-increment: ordered-list;
			position: relative;
			padding-left: 30px;

			&:before {
				content: counter(ordered-list) '.';
				margin-right: 18px;
				flex-shrink: 0;
				position: absolute;
				top: 0;
				left: 0;
				color: #fa3a00;
				font-size: 16px;
				font-family: $FONT;
			}
		}
	}

	ul {
		list-style: none;
		margin-bottom: 20px;

		li {
			position: relative;
			display: flex;
			margin-bottom: 6px;

			&:before {
				content: '';
				height: 4px;
				width: 4px;
				display: block;
				background: #fa3a00;
				transform: rotateZ(45deg);
				margin-right: 18px;
				flex-shrink: 0;
				position: relative;
				top: 13px;
			}
		}
	}

	a {
		position: relative;
		display: inline-block;
		color: #797672;
		transition: color 0.3s;

		&:before {
			content: '';
			width: 100%;
			position: absolute;
			bottom: 0px;
			left: 0;
			height: 1px;
			background: #fa3a00;
		}

		&:hover {
			color: #2f2425;
		}
	}
}
