.ExploreNav {
	position: absolute;
	display: flex;
	flex-flow: column;
	justify-content: center;
	opacity: 0;
	visibility: hidden;
	top: 20px;
	bottom: 0px;
	left: calc(0px + env(safe-area-inset-left));
	z-index: 2;
	@media (min-height: $BREAKPOINT_HEIGHT) {
		top: 120px;
		bottom: 40px;
	}
	@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
		top: 0px;
		bottom: 40px;
		left: calc(#{$TABLET_MARGIN - 18px} + env(safe-area-inset-left));
	}
	@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: 1000px) {
		top: 200px;
		bottom: 200px;
		left: calc(#{$TABLET_MARGIN - 18px} + env(safe-area-inset-left));
	}

	@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: $BREAKPOINT_HEIGHT) {
		top: 0px;
		bottom: 40px;
		left: 51px;
	}
	@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: 1000px) {
		top: 200px;
		bottom: 200px;
		left: 51px;
	}
	width: 50px;
	overflow: hidden;
	transition: width 0s 0.3s;

	@media (hover: hover) {
		&:hover {
			transition: width 0s 0s;
			width: 230px;
		}
	}

	.ExploreNav-inner {
		position: relative;
		display: flex;
		flex-flow: column;
		justify-content: center;
		width: 230px;
		padding-top: 60px;
		padding-bottom: 60px;
		@media (hover: hover) {
			&:hover {
				span {
					opacity: 0.7;
				}

				.ExploreNav-diamond:not(.end) {
					.innerDiamond {
						transition: 0.15s;
						transform: scale(0.2) !important;
						opacity: 0 !important;
					}

					span {
						transition: 0.2s;
						opacity: 1 !important;
					}
				}
			}
		}
	}

	a {
		margin-bottom: 31px;
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
			margin-bottom: 64px;
		}
		display: flex;
		flex-flow: row;
		align-items: center;
		height: 26px;
		padding-left: 20px;
		position: relative;
		white-space: nowrap;
		transform: translate3d(0, 0, 0);

		& > span {
			opacity: 0;
			padding-left: 24px;
			display: inline-block;
			font-size: 11px;
			margin-top: 0px;
			font-family: $font-rogan;
			color: rgba(58, 48, 43, 0.8);
			font-weight: $font-weight-rogan-bold;
			text-transform: uppercase;
			line-height: 1.273;
			transition: 0.3s;
		}

		@media (hover: hover) {
			&:hover {
				span {
					opacity: 1 !important;
					color: $COLOR_ORANGE;
				}
			}
		}

		.ExploreNav-line {
			position: absolute;
			height: 60px;
			top: 29px;
			left: 21px;
			width: 2px;
			overflow: hidden;
			@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
				height: 120px;
				top: 30px;
				left: 22px;
				width: 2px;
			}
		}

		.ExploreNav-lineInner {
			position: absolute;
			height: 100%;
			top: 0px;
			left: 0px;
			width: 2px;
			opacity: 0.7;
			background-image: url("data:image/svg+xml,%3Csvg width='2' height='2' viewBox='0 0 2 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='1' cy='1' r='1' fill='%2378746D'/%3E%3C/svg%3E%0A");
			transform: translateY(-50%);
			@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
				opacity: 1;
				width: 2px;
				background-image: url("data:image/svg+xml,%3Csvg width='2' height='2' viewBox='0 0 2 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='1' cy='1' r='1' fill='%23D2CCC5'/%3E%3C/svg%3E%0A");
				//background-image: url("data:image/svg+xml,%3Csvg width='2' height='2' viewBox='0 0 2 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='1' cy='1' r='1' fill='%23ff2080'/%3E%3C/svg%3E%0A");
			}
			background-size: 2px 6px;
			background-repeat: repeat-y;
		}

		.ExploreNav-lineProgress {
			position: absolute;
			height: 66px;
			top: 22px;
			left: 21px;
			width: 2px;
			overflow: hidden;
			@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
				height: 128px;
				top: 23px;
				left: 22px;
				width: 2px;
			}
			background: $COLOR_ORANGE;
			transform-origin: top center;
			transform: scaleY(0);
			@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
				width: 2px;
			}
		}

		&:last-child {
			//height: 33px;
			position: relative;
			top: -27px;
			margin-top: 22px;
			margin-bottom: -27px;
			@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
				//height: 60px;
				top: -30px;
				margin-top: 22px;
				margin-bottom: -30px;
			}
		}
	}

	.ExploreNav-diamond {
		width: 4px;
		height: 4px;
		display: block;
		transform: rotateZ(45deg);
		position: relative;
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
			width: 6px;
			height: 6px;
		}

		span {
			opacity: 0;
			display: block;
			width: 22px;
			text-align: center;
			position: absolute;
			font-size: 9px;
			top: 50%;
			left: -3px;
			font-feature-settings: 'tnum';
			font-variant-numeric: tabular-nums;
			transform-origin: center center;
			transform: rotateZ(-45deg) translate3d(0, -8.5px, 0);
			@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
				width: 28px;
				font-size: 12px;
				top: 45%;
				left: -4px;
				transform: rotateZ(-45deg) translate3d(0, -11px, 0);
			}

			font-family: $font-rogan;
			color: rgb(22, 23, 27);
			font-weight: $font-weight-rogan-bold;
			transition: 0.3s;
		}

		.outerDiamond {
			width: 11px;
			height: 11px;
			display: block;
			position: absolute;
			box-sizing: content-box;
			top: -4px;
			left: -4px;
			@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
				width: 15px;
				height: 15px;
				top: -6px;
				left: -6px;
			}

			z-index: -1;
			border: 1px solid rgba(51, 47, 42, 0.2);
			//opacity: 0.2;
		}

		.innerDiamond {
			width: 5px;
			height: 5px;
			background: #332f2a;
			display: block;
			position: absolute;
			box-sizing: content-box;
			z-index: -1;
			top: 0px;
			left: 0px;
			will-change: transform;
			transition: 0.3s;
			transform: scale(1);
			//opacity: 0.2;
		}

		.highlightDiamond {
			width: 10px;
			height: 10px;
			display: block;
			position: absolute;
			box-sizing: content-box;
			top: -5px;
			left: -5px;
			opacity: 0;
			@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
				width: 15px;
				height: 15px;
				top: -7px;
				left: -7px;
			}

			z-index: -1;
			border: 2px solid $COLOR_ORANGE;
		}

		.highlightDiamondOuter {
			width: 16px;
			height: 16px;
			display: block;
			position: absolute;
			box-sizing: content-box;
			top: -7px;
			left: -7px;
			opacity: 0;
			@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
				width: 25px;
				height: 25px;
				top: -11px;
				left: -11px;
			}

			z-index: -1;
			border: 1px solid $COLOR_ORANGE;
		}
	}
}
