.EndScreen {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	flex-flow: column;
	justify-content: center;
	align-items: center;
	user-select: none;
	pointer-events: none;
	@media (max-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT - 1px) {
		padding-top: 40px;
	}
	.EndScreen-trailer {
		opacity: 1;
		visibility: visible;
		position: relative;
		top: 0;
		right: 0;
		display: block;
		margin-top: 10px;
		@media (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
			margin-top: 30px;
		}
		/*		@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: $BREAKPOINT_HEIGHT) {
			position: absolute;
			margin-top: 0px;
			top: 54px;
			bottom: auto;
			right: calc(54px + env(safe-area-inset-right));
		}*/
		@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: $BREAKPOINT_HEIGHT) {
			margin-top: 50px;
		}
		img {
			width: calc(272 / 1440 * 274vw);
			height: calc(169 / 1440 * 274vw);
			@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
				width: calc(272 / 1440 * 100vw);
				height: calc(169 / 1440 * 100vw);
			}
		}
	}

	.EndScreen-ctas {
		display: flex;
		flex-flow: column;
		align-items: center;
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
			flex-flow: row;
		}

		a:last-child {
			margin-left: 0px;
			margin-top: 10px;
			@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
				margin-top: 0px;
				margin-left: 24px;
			}
		}

		a {
			flex-grow: 0;
			height: 40px;
			@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
				height: 50px;
			}
			&.with-borders {
				border: 1px solid rgba(#d0ccc8, 0.4);

				&:before {
					border: 1px solid rgba(#d0ccc8, 1);
				}
			}
		}
	}

	.EndScreen-outro {
		display: flex;
		flex-flow: column;
		align-items: center;
		text-align: center;
		@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: 900px) {
			margin-top: 100px;
		}
		h2 {
			font-family: $font-roobert;
			font-weight: $font-weight-roobert-light;
			@include poly-fluid-sizing(
				'font-size',
				(
					320px: 27px,
					375px: 30px,
					768px: 30px,
					1680px: 35px
				)
			);
			@include poly-fluid-sizing(
				'line-height',
				(
					320px: 32px,
					375px: 38px,
					768px: 38px,
					1680px: 44px
				)
			);
			color: rgb(22, 23, 27);
		}

		p {
			font-family: $font-roobert;
			font-weight: $font-weight-roobert-regular;
			letter-spacing: 0.04em;
			@include poly-fluid-sizing(
				'font-size',
				(
					320px: 14px,
					375px: 16px,
					768px: 16px,
					1680px: 19px
				)
			);
			@include poly-fluid-sizing(
				'line-height',
				(
					320px: 18px,
					375px: 24px,
					768px: 24px,
					1680px: 28px
				)
			);
			color: rgba(22, 23, 27, 0.7);
			margin-bottom: 12px;
			margin-top: 12px;
			padding-left: 30px;
			padding-right: 30px;
			br {
				display: none;
			}
			@media (min-width: 340px) {
				padding-left: 0px;
				padding-right: 0px;
				br {
					display: unset;
				}
			}
			@media (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
				margin-bottom: 32px;
				margin-top: 22px;
			}
			@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
				margin-top: 32px;
				margin-bottom: 42px;
			}
		}
	}

	.fadeIn {
		opacity: 0;
		will-change: transform;
	}
}
