.HeaderModule {
	padding-top: 20px;
	position: fixed;
	top: 0;
	left: calc(10px + env(safe-area-inset-left));
	right: calc(10px + env(safe-area-inset-right));
	z-index: 11;
	pointer-events: none;
	@include bp(xxs) {
		left: calc(18px + env(safe-area-inset-left));
		right: calc(18px + env(safe-area-inset-right));
	}

	@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
		left: calc(#{$TABLET_MARGIN} + env(safe-area-inset-left));
		right: calc(#{$TABLET_MARGIN} + env(safe-area-inset-right));
		top: $TABLET_MARGIN - 20px;
	}
	@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: $BREAKPOINT_HEIGHT) {
		left: calc(60px + env(safe-area-inset-left));
		right: calc(60px + env(safe-area-inset-right));
		top: 40px;
	}

	&.disabled {
		.HeaderModule-logoNav {
			pointer-events: none;

			.BurgerMenu-icon {
				display: none;
			}
		}
	}

	.TopBarInner {
		display: flex;
		justify-content: space-between;
		flex-flow: row;
		align-items: center;
		position: relative;
		z-index: 5;
		width: 100%;
		.HeaderModule-logoNav {
			display: flex;
			flex-flow: row;
			justify-content: center;
			line-height: 0;
			pointer-events: auto;
		}
		.Logo {
			width: 114px;
			height: 22px;
			@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: $BREAKPOINT_HEIGHT) {
				width: 159px;
				height: 25px;
			}
		}

		.BurgerMenu-icon {
			width: 30px;
			display: flex;
			flex-flow: column;
			cursor: pointer;
			margin-right: 10px;
			margin-top: 1px;
			position: relative;
			&:before {
				content: '';
				position: absolute;
				left: calc(50% - 25px);
				top: calc(50% - 30px);
				width: 50px;
				height: 50px;
				display: block;
			}
			@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
				margin-top: 2px;
				margin-right: 33px;
			}

			.BurgerMenu-icon-line {
				height: 3px;
				background: #fa3a00;
				margin-bottom: 4px;
				width: 18px;
				will-change: transform;
				transition: transform 0.2s $ease-in-out-sine, background-color 0.4s;
				&:nth-child(1) {
					transform: translateX(2px);
				}

				&:nth-child(2) {
					transform: translateX(7px);
				}

				&:nth-child(3) {
					margin-bottom: 0;
				}

				@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
					width: 24px;
					transform: translateX(0) scaleX(1);
					&:nth-child(1) {
						transform: translateX(3px) scaleX(22 / 24 * 1);
					}

					&:nth-child(2) {
						transform: translateX(10px) scaleX(20 / 24 * 1);
					}
				}
			}
			@media (hover: hover) {
				&:hover {
					.BurgerMenu-icon-line {
						transform: translateX(4px) scaleX(1);
					}
				}
			}
		}
		.TopBarCtas {
			display: flex;
			align-items: center;
			a {
				pointer-events: auto;
			}

			span {
				transition: color 0.5s;
				pointer-events: none;
			}
			.ButtonCta.firstCTA {
				display: none;

				@include bp(sm) {
					display: flex;
				}

				.dark-mode & {
					color: #ffffff;
					border-color: rgba($COLOR_WHITE, 0.4);

					&:before {
						border-color: rgba($COLOR_WHITE, 1);
					}
				}
			}

			.ButtonCta {
				margin-left: 20px;
				height: 40px;
				@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
					height: 50px;
				}
				.dark-mode & {
					.arrow:before {
						background-image: url('../../assets/svg/embedded/chevron_right-dark.svg');
					}

					.arrow:after {
						background-image: url('../../assets/svg/embedded/chevron_right-dark.svg');
					}
				}
			}
		}
	}
}
