html.AffiliatesModuleInPage {
	.BurgerMenu-icon {
		display: none !important;
	}
}
.AffiliatesModule {
	.moduleInner {
		min-height: 100vh;
		padding-bottom: 140px;
		padding-top: 150px;

		@include bp(sm) {
			padding-top: 250px;
			padding-bottom: 200px;
			min-height: 100vh;
		}

		@include bp(md) {
			padding-top: 200px;
			padding-bottom: 200px;
			min-height: 100vh;
		}
	}

	.AffiliatePreviewHeader {
		p {
			margin-bottom: 0.8em;
		}

		h2 {
			font-size: 28px;
			line-height: 34px;
			max-width: 510px;
			margin-bottom: 0.5em;

			@include bp(sm) {
				font-size: 44px;
				line-height: 56px;
			}
		}
	}

	.affiliates {
		margin-top: 20px;
	}

	.borderLine {
		padding-right: calc(3 * 1.00629%);

		span {
			display: block;
			border-bottom: 1px solid rgba(#78746d, 0.2);
		}
	}

	.AffiliatePreview {
		width: 100%;
		text-decoration: none;
		padding: 37px 0 32px 0;
		display: flex;
		flex-direction: column-reverse;

		@include bp(md) {
			padding: 40px 0;
			flex-direction: row;
		}

		&:last-child {
			border-bottom: 0;
			padding-bottom: 0;
		}

		.AffiliateThumbnail {
			flex-shrink: 0;
			@extend .borderedImage;
			margin-bottom: 5px;
		}

		.copy {
			display: flex;
			align-items: center;
		}

		.AffiliatePreviewContent {
			.ContentTopBar {
				display: flex;
				align-items: center;
				justify-content: space-between;
				margin-bottom: 24px;
				margin-top: 16px;

				@include bp(md) {
					margin-top: 0;
				}

				p {
					font-family: $font-rogan;
					font-weight: $font-weight-rogan-bold;
					color: rgba($COLOR_TEXT, 0.6);
					letter-spacing: 0.24em;
					text-transform: uppercase;
					line-height: 1.333em;
					font-size: 9px;

					@include bp(md) {
						font-size: 12px;
					}
				}
			}

			.AffiliateTitle {
				font-size: 30px;
				line-height: 38px;

				@include bp(md) {
					font-size: 30px;
					line-height: 38px;
				}
			}

			.AffiliateContent {
				font-size: 15px;
				font-style: normal;
				font-weight: 400;
				line-height: 22px;
				letter-spacing: 0.002em;
				text-align: left;
				margin-bottom: 3em;
				opacity: 0.7;

				@include bp(md) {
					font-size: 16px;
					line-height: 28px;
				}
			}

			.link {
				@extend .arrow-hover-left;
				width: 112px;
				height: 40px;
				display: flex;
				align-items: center;
				padding-left: 40px;
				font-size: 11px;

				&:after {
					right: 20px;
					top: calc(50% - 2px);
				}

				&:before {
					right: 22px;
					top: calc(50% - 6px);
				}
			}
		}
	}
}
