h1 {
	font-size: 44px;
	font-family: $font-roobert;
	font-weight: $font-weight-roobert-light;
	line-height: em(56px, 44px);
	@include poly-fluid-sizing(
		'font-size',
		(
			$BREAKPOINT_MOBILE_BIG: 28px,
			$BREAKPOINT_DESKTOP_BIG: 44px
		)
	);
}

h2 {
	color: $COLOR_TEXT;
	font-size: 24px;
	line-height: 1.2em;
	font-weight: $font-weight-roobert-light;
	margin-bottom: 16px;
	@include poly-fluid-sizing(
		'font-size',
		(
			$BREAKPOINT_MOBILE_BIG: 24px,
			$BREAKPOINT_DESKTOP_BIG: 30px
		)
	);

	@include bp(sm) {
		margin-bottom: 32px;
	}
}

.text-16 {
	font-size: 14px;
	font-family: $FONT;
	font-weight: 400;
	color: $COLOR_TEXT;
	letter-spacing: 0.02em;
	line-height: em(20px, 14px);

	@include bp(sm) {
		font-size: 14px;
	}
}

.text-14 {
	font-size: 12px;
	font-family: $FONT;
	font-weight: 400;
	color: $COLOR_TEXT;
	letter-spacing: 0.02em;
	line-height: em(18px, 12px);

	@include bp(sm) {
		font-size: 14px;
	}
}

.uppercase {
	text-transform: uppercase;
	letter-spacing: 0.24em;
	font-family: $font-rogan;
	font-weight: $font-weight-rogan-bold;
}

.eyebrow {
	font-family: $font-roobert;
	font-weight: 400;
	letter-spacing: 0.06em;
	color: rgba($COLOR_TEXT, 0.7);
	font-size: 12px;
}

.eyebrow-red {
	@extend .eyebrow;
	color: $COLOR_RED;
}
