.FooterModule {
	padding-bottom: 50px;
	padding-top: 280px;
	margin-top: -140px;
	position: relative;
	overflow: hidden;
	//background: #102434;
	background-color: $COLOR_BEIGE;
	z-index: -1;

	@include bp(sm) {
		padding-bottom: 100px;
	}

	@include bp(sm) {
		padding-bottom: 50px;
	}

	.FooterModule-noise {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		top: 0;
		pointer-events: none;
		z-index: 4;
	}
	&:before {
		content: '';
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
		min-width: 1680px;
		min-height: 642px;
		width: 100%;
		//padding-bottom: 29.7619047619%;
		//height: 500px;
		//background: url('../../assets/images/footerBackground.png') no-repeat;
		background: url('../../assets/svg/footer.svg') no-repeat;
		background-position: top center;
		background-size: cover;
		z-index: 4;
		box-sizing: border-box;
		pointer-events: none;

		@include bp(xl) {
			min-height: 650px;
		}
	}

	footer {
		position: relative;
		z-index: 8;
	}

	.HighlightedLinks {
		margin-bottom: 18px;
		position: relative;
		display: flex;
		align-items: center;
		flex-direction: column;

		@include bp(sm) {
			margin-bottom: 36px;
		}

		.FooterLogo {
			position: absolute;
			top: -20px;
			left: calc(50% - 124px);
			z-index: 6;
			opacity: 0.2;
			width: 248px;

			@include bp(sm) {
				width: 550px;
				top: -60px;
				left: calc(50% - 275px);
			}

			svg {
				max-width: 100%;
			}
		}

		.Link {
			text-align: center;
			margin-bottom: 23px;
			color: #dce1ec;
			font-size: 18px;
			letter-spacing: 0.02em;
			position: relative;
			z-index: 8;
			font-weight: $font-weight-roobert-light;
			padding-right: 42px;

			&:before {
				position: absolute;
				content: '';
				top: calc(50% - 4px);
				right: 2px;
				height: 16px;
				width: 25px;
				background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg width='16px' height='9px' viewBox='0 0 16 9' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Ctitle%3EGroup 2%3C/title%3E%3Cg id='Symbols' stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3E%3Cg id='Group' transform='translate(9.292893, 4.535534) scale(-1, 1) translate(-9.292893, -4.535534) translate(-0.000000, 1.000000)'%3E%3Cg id='Group-2' transform='translate(3.585786, 0.000000)'%3E%3Cpolygon id='Rectangle-Copy-4' stroke='%23FA3B02' transform='translate(3.535534, 3.535534) rotate(-315.000000) translate(-3.535534, -3.535534) ' points='1.03553391 1.03553391 6.03553391 1.03553391 6.03553391 6.03553391 1.03553391 6.03553391'%3E%3C/polygon%3E%3Crect id='Rectangle' fill='%23FA3B02' transform='translate(11.000000, 3.500000) scale(-1, 1) translate(-11.000000, -3.500000) ' x='7' y='3' width='8' height='1'%3E%3C/rect%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
				background-repeat: no-repeat;
				background-size: contain;
				transition: transform 0.3s;
			}

			&:after {
				content: '';
				height: 6px;
				width: 6px;
				border: 1px solid #f53a02;
				position: absolute;
				box-sizing: border-box;
				right: 0px;
				top: calc(50%);
				transform: rotateZ(45deg);
				transition: transform 0.3s;
				opacity: 0.4;
			}

			@include bp(md) {
				padding-right: 36px;

				&:before {
					right: 2px;
				}

				&:after {
					right: 0px;
				}

				&:hover {
					&:before {
						transform: translateX(6px);
					}
				}
			}
		}
	}

	.Separator {
		@include bp(sm) {
			display: none;
		}
	}

	.FooterLinks {
		display: flex;
		flex-direction: column;
		width: 100%;
		justify-content: center;

		@include bp(sm) {
			justify-content: space-between;
			align-items: flex-end;
			flex-direction: row;
		}
	}

	.FooterNav {
		width: 100%;
		display: flex;
		flex-flow: row;
		padding: 32px 0;

		ul,
		li {
			list-style: none;
			padding: 0;
			margin: 0;
		}
		ul {
			width: 50%;
		}
		@include bp(sm) {
			max-width: 250px;
			padding: 0;
		}

		li {
			width: 100%;
			margin-top: 12px;
			display: block;
		}
		a {
			display: block;
			width: 100%;
			color: #dce1ec;
			font-family: $font-rogan;
			font-weight: $font-weight-rogan-bold;
			font-size: 11px;
			text-transform: uppercase;
			letter-spacing: 0.24em;
			text-align: center;
			position: relative;
			transition: opacity 0.2s;
			&:hover {
				opacity: 0.5;
			}

			&:nth-child(1),
			&:nth-child(2) {
				margin-top: 0;
			}

			@include bp(sm) {
				text-align: left;
			}

			&:before {
				position: absolute;
				height: 4px;
				width: 4px;
				background: rgba(#90a3cf, 0.5);
				transform: rotateZ(45deg);
				top: 4px;
				left: -10px;

				@include bp(sm) {
					content: '';
				}
			}
		}
	}

	.FooterSocials {
		text-align: center;
		position: relative;

		@include bp(sm) {
			text-align: right;
		}

		.CatchPhrase {
			color: #dce1ec;
			font-family: $font-rogan;
			font-weight: $font-weight-rogan-bold;
			font-size: 11px;
			margin-bottom: 20px;
			text-transform: uppercase;
			letter-spacing: 0.24em;
			opacity: 0.6;
			display: none;

			@include bp(sm) {
				display: block;
			}
		}

		.SocialsList {
			display: flex;
			align-items: center;
			padding-top: 32px;
			border-top: 1px solid rgba(#fff, 0.2);
			position: relative;

			@include bp(sm) {
				padding-top: 0;
				border: none;
				margin-top: 20px;
			}

			a {
				margin-left: 16px;
				line-height: 0;
				opacity: 1;
				transition: opacity 0.2s;
				padding: 4px;

				&:nth-child(1) {
					margin-left: 0;
				}

				@include bp(sm) {
					opacity: 0.6;

					&:hover {
						opacity: 1;
					}
				}
			}
		}
	}
}
