.CookieBar {
	background-color: #392526;
	color: #fff;
	padding: 25px;
	position: fixed;
	bottom: 15px;
	left: 50%;
	transform: translateX(-50%);
	z-index: 100;
	border-radius: 6px;
	width: 80%;
	max-width: 850px;
	display: none;
	opacity: 0;

	@include bp(tp) {
		padding: 35px;
	}

	.wrapper {
		display: flex;
		align-items: center;
		flex-direction: column;

		@include bp(tp) {
			flex-direction: row;
		}

		p {
			font-size: 15px;
			line-height: 21px;
			margin-bottom: 20px;

			@include bp(tp) {
				margin-right: 50px;
				margin-bottom: 0;
			}
		}

		.cta {
			span {
				font-weight: 500;
			}

			a {
				color: #fff;
				text-decoration: underline;
			}
		}
	}

	.allowCookies {
		background-color: #fff;

		.title {
			white-space: nowrap;
			margin-right: 10px;
		}
	}

	.CookieBar-close {
		position: absolute;
		top: 7px;
		right: 7px;
		z-index: 1;
		background: transparent;
		padding: 0;
		border: 0;
		opacity: 0.5;
		cursor: pointer;

		&:hover {
			opacity: 1;
		}

		svg {
			height: 7px;
			width: 7px;
			margin: 3px;
			display: block;
		}
	}
}
