#Loading {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 0;
	background: #f1eae1;
	display: flex;
	align-items: center;
	justify-content: center;
	background-image: url('data:image/png;base64,'),
		linear-gradient(180deg, #f2f2f2 33%, #e9e5e1 100%);
	background-size: 30px 30px, 100%;
	background-repeat: repeat, no-repeat;
	will-change: transform;
	transition: opacity 1.2s;
	svg {
		width: 154px;
		height: 239px;
	}
}
body.loaded {
	#Loading {
		opacity: 0;
	}
}
