.HomeTemplate {

	.templateInner {
		width: 100%;
		height: 100vh;
		position: relative;
		z-index: 4;
	}

	.video-trailer {
		height: 100%;
		width: 100%;
		background: black;
		cursor: pointer;
		z-index: 10;

		video {
			height: 100%;
			width: 100%;
			//object-fit: fill;
		}

		.vjs-control-bar {
			display: flex;
			position: absolute;
			bottom: 60px;
			right: 0;
			justify-content: flex-end;
			align-items: center;
			padding-right: 60px;

			.vjs-progress-control {
				height: 10px;
				width: 62vw;
				display: flex;
				align-items: center;
				cursor: pointer;
				margin-right: 74px;
				order: 1;
			}

			.video-title {
				position: absolute;
				bottom: 26px;
				left: 0;
				color: #fff;
				font-size: 14px;
				font-family: $font-rogan;
				font-weight: $font-weight-rogan-bold;
			}

			.vjs-mouse-display,
			.vjs-load-progress,
			.vjs-time-tooltip {
				display: none;
			}

			.vjs-progress-holder {
				height: 2px;
				width: 100%;
				background: rgba(#ffffff, 0.2);
				position: relative;
			}

			.vjs-play-progress {
				height: 2px;
				background: #fa3b02;
				position: absolute;
				top: 0;
				left: 0;
			}


			.vjs-menu-button-popup.vjs-control {
				.vjs-control-text, .vjs-resolution-button-label {
					color: #fff;
					font-family: $font-rogan;
					font-weight: $font-weight-rogan-bold;
					font-size: 14px;
				}

				.vjs-menu {
					right: 0;
					left: auto;
				}

				.vjs-menu-item {
					font-size: 14px;
					text-align: right;

					.vjs-control-text {
						display: none;
					}
				}
			}
			.vjs-menu-button-popup:not(.vjs-subs-caps-button) {
				order: 2;
				position: relative;
				padding-right: 13px;
				height: 20px;
				line-height: 0;
				.vjs-menu {
					right: 26px;
					left: auto;
				}
				button {
					padding-right: 13px;
					//padding: 0;
					-webkit-appearance: none;
					appearance: none;
					background: transparent;
					border: none;
					outline: none;
					//display: none;
				}
				.vjs-resolution-button-label {
					display: none;
				}
			}
			.vjs-subs-caps-button.vjs-control {
				position: relative;
				order: 4;
				height: 20px;
				line-height: 0;

				.vjs-subs-caps-button {
					background: transparent;
					border: 0;
					padding: 0;
				}


			}

			.vjs-fullscreen-control {
				order: 2;
				color: transparent;
				background: transparent;
				width: 18px;
				height: 16px;
				padding: 0;
				border: none;
				cursor: pointer;
				z-index: 2;

				&:before {
					height: 2px;
					width: 2px;
					background-color: #fff;
					opacity: 0.4;
					content: "";
					position: absolute;
					top: calc(50% - 1px);
					left: -13px;
				}
				.fullscreen-icon {
					width: 18px;
					height: 16px;
					position: absolute;
					top: 0;
					left: 0;
					background: url("../../assets/images/full_screen_off.svg") no-repeat;
				}

				.vjs-control-text {
					width: 18px;
					display: block;
				}
			}


			.vjs-volume-panel {
				order: 3;
				position: relative;
				margin: 0 24px;
				z-index: 4;

				&:before,
				&:after {
					height: 2px;
					width: 2px;
					background-color: #fff;
					opacity: 0.4;
					content: "";
					position: absolute;
					top: calc(50% - 1px);
				}

				&:before {
					left: -13px;
				}

				&:after {
					right: -13px;
				}

				.vjs-mute-control {
					position: absolute;
					top: 0;
					left: 0;
					color: transparent;
					background: transparent;
					opacity: 0;
					cursor: pointer;
				}

				.volumeBars {
					display: flex;
					height: 20px;
					width: 20px;
					align-items: center;
					justify-content: center;

					.bar {
						height: 3px;
						width: 2px;
						margin-right: 2px;
						will-change: transform;
						background-color: #fff;
						transform-origin: bottom center;

						&:last-child {
							margin-right: 0;
						}

						&:nth-child(2n + 1) {
							opacity: 0.4;
						}
					}
				}
			}

			.vjs-menu-content {
				bottom: -20px;
			}
		}

	}

	.videoWrapper {
		position: relative;
		height: 100%;
		width: 100%;
		pointer-events: none;
		opacity: 0;
		visibility: hidden;

		.vjs-loading-spinner {
			.vjs-control-text {
				display: none !important;
			}
		}
		.VideoLayer {
			position: fixed;
			top: 0;
			height: 100%;
			width: 100%;
			left: 0;
			z-index: 20;
		}

		.playButton {
			position: absolute;
			top: 0;
			left: 0;
			opacity: 1;
			transition: opacity .3s .1s;
			z-index: 6;
			pointer-events: none;

			&:before {
				position: absolute;
				content: "";
				height: 80px;
				width: 80px;
				border: 2px solid #fff;
				transform: rotateZ(45deg);
				left: -41px;
				top: -41px;
				transition: transform 0.5s;
			}

			&:after {
				position: absolute;
				content: "";
				height: 100px;
				width: 100px;
				border: 1px solid rgba(#fff, 0.2);
				transform: rotateZ(45deg);
				left: -50px;
				top: -50px;
				transition: transform 0.5s 0.1s;
			}

			.pauseIcon {
				position: relative;
				width: 6px;
				opacity: 0;

				&:before,
				&:after {
					content: '';
					position: absolute;
					height: 16px;
					width: 3px;
					background: #fff;
					top: -8px;
				}

				&:before {
					left: -3px;
				}

				&:after {
					right: -3px;
				}
			}

			.playIcon {
				width: 0;
				height: 16px;
				border-top: 8px solid transparent;
				border-bottom: 8px solid transparent;
				border-left: 10px solid #fff;
				position: absolute;
				top: -8px;
				left: 0;
			}

			&.is-playing {
				.playIcon {
					opacity: 0;
				}

				.pauseIcon {
					opacity: 1;
				}
			}

			&.user-inactive {
				opacity: 0;

				&:after,
				&:before {
					transform: rotateZ(45deg) scale(0);
				}
			}

		}


	}

}


:root {
	--leftMargin: 68px;
}

html.HomeTemplatePage {
	font-size: 22px;
	font-family: $FONT;
	width: 100%;
	height: 100%;
	vertical-align: baseline;
	font-weight: $font-weight-roobert-light;
	margin: 0;
	overflow: hidden;
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
	-webkit-tap-highlight-color: transparent;

	.HeaderModule .TopBarInner {
		.TopBarCtas {
			a.with-borders {
				border: 1px solid rgba(#d0ccc8, 0.4);

				&:before {
					border: 1px solid rgba(#d0ccc8, 1);
				}
			}
		}
	}


	&.darkMenu {
		.HeaderModule .TopBarInner {
			.TopBarCtas {
				a.with-borders {
					border: 1px solid rgba(#d0ccc8, 0.2);

					&:before {
						border: 1px solid rgba(#d0ccc8, 0.4);
					}
				}
			}

			.BurgerMenu-icon {
				.BurgerMenu-icon-line {
					background: #322FB3;
				}
			}
		}
	}

	#TemplateLayer, #SiteWrapper {
		width: unset;
		overflow: unset;
		min-height: unset;
		position: unset;
	}

	body {
		font-size: 22px;
		font-family: $FONT;
		width: 100%;
		height: 100%;
		min-height: unset;
		vertical-align: baseline;
		font-weight: $font-weight-roobert-light;
		margin: 0;
		overflow: hidden;
		-moz-osx-font-smoothing: grayscale;
		-webkit-font-smoothing: antialiased;
		-webkit-tap-highlight-color: transparent;
		//height: -webkit-fill-available;
		//height: fill-available;
		position: fixed;
		//overflow: hidden;
		background-color: $COLOR_WHITE;
		color: $COLOR_BLACK;

		overscroll-behavior-y: none;
		overscroll-behavior-x: none;
	}

	#App {
		top: 0;
		left: 0;
		position: absolute;
		width: 100%;
		height: 100%;
		overflow: hidden;
		//bottom: 0;
		overflow-y: scroll;
		overscroll-behavior: contain;
		//height: 100vh;
		//height: -moz-available;          /* WebKit-based browsers will ignore this. */
		//height: -webkit-fill-available;  /* Mozilla-based browsers will ignore this. */
		//height: fill-available;
	}

	#StickyOverlay {
		position: sticky;
		top: 0;
		width: 100%;
		height: 100%;
		overflow: hidden;
		z-index: 2;
	}

	#ScrollSpacer {
		height: calc(100vh * 10);
		width: 0;
		position: relative;
		top: -100%;
		//display: none;
		//pointer-events: ;
	}

	#ViewContainer {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		//z-index: -1;
		overflow-y: scroll;
		overflow-x: hidden;
		height: 100%;
		bottom: 0;
		right: 0;
		//display: none;

	}


	canvas {
		outline: none;
	}

	body.hide-debug {
		#ViewContainer {
			div:last-child {
				display: none !important;
			}
		}

		.gsap-marker-end, .gsap-marker-start, .gsap-marker-scroller-start, .gsap-marker-scroller-end {
			display: none !important;
		}
	}

	.dg.ac {
		z-index: 4 !important;
		//display: none !important;
	}

	body.camera-debug {
		#ViewContainer {
			z-index: 1;
		}

		.dg.ac {
			z-index: 2;
		}
	}

	body.show-fps {
		#ViewContainer {
			div:last-child {
				z-index: 4;
				display: block !important;
			}
		}
	}

	body:not(.hide-debug) {
		.dg.ac {
			.dg.main {
				display: block !important;
			}
		}
	}
}

html.non-sticky {
	overflow: unset;
	height: unset;
	overflow-y: scroll;
	overscroll-behavior: contain;
	overscroll-behavior-y: none;
	overscroll-behavior-x: none;

	body {
		position: unset;
		overflow: unset;
		height: unset;
	}

	#App {
		position: unset;
		height: unset;
		overflow: unset;
	}

	#ViewContainer {
		overflow: hidden;
	}

	#StickyOverlay {
		position: static;
	}

	.BurgerMenu-icon, .Logo, .ExploreNav, .LandingMenuIntroWrapper, .PlayNow, .LandingEnter-largeDiamond, .LandingEnter, .ExploreButton, .MakeChoice {
		position: fixed;
	}

	.BurgerMenu-icon, .Logo, .ExploreNav, .LandingMenuIntroWrapper, .PlayNow, .LandingEnter-largeDiamond, .LandingEnter {
		contain: content;
	}

	.ChapterPOIs, .BottomBar-right {
		position: fixed;
	}

	#ScrollSpacer {
		position: unset;
		top: unset;
	}

	.ChapterIntro-overlay {
		position: fixed;
		z-index: 1;
		//height: 100vh;
		//position: unset;
		/*        .ChapterIntro {
					z-index: 1;
					position: fixed;
					width: 500px;
					height: 340px;
					top: calc(50% - 170px);
					left: calc(50% - 250px);
				}*/
	}
}
