.ArticlesModule {
	.moduleInner {
		min-height: 100vh;
		padding-bottom: 140px;
		padding-top: 150px;

		@include bp(sm) {
			padding-top: 250px;
			padding-bottom: 200px;
			min-height: 100vh;
		}

		@include bp(md) {
			padding-top: 200px;
			padding-bottom: 200px;
			min-height: 100vh;
		}
	}

	h3 {
		margin-bottom: 20px;
	}

	a {
		text-decoration: underline;
	}

	.NewsMainCategoriesTitle {
		@extend .uppercase;
		padding-bottom: 12px;
		border-bottom: 1px solid rgba(#524d45, 0.2);
		font-size: 11px;

		@include bp(md) {
			display: none;
		}
	}

	.NewsMainCategories {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -8px;

		@include bp(md) {
			display: block;
			max-width: 232px;
			margin: 130px 0 0 0;
		}

		.NewsCategory {
			width: 50%;
			padding: 10px 8px 0 8px;
			box-sizing: border-box;
			position: relative;

			@include bp(md) {
				width: 100%;
			}

			.MainCategoryTitle {
				color: $COLOR_TEXT;
				font-size: 13px;
				line-height: 1.4em;
				padding: 15px 0 15px 0;
				text-decoration: none;
				@extend .dotted-border;

				& > * {
					pointer-events: none;
				}

				&:after {
					width: calc(100% - 18px);
					left: auto;
					right: 0;
				}

				@include bp(md) {
					border-bottom: 1px solid rgba(#524d45, 0.2);
					font-size: 20px;

					&:after {
						content: none;
					}
				}

				&:before {
					content: '';
					height: 1px;
					position: absolute;
					bottom: 0px;
					right: 0px;
					width: 0;
					background: $COLOR_RED;
					transition: width 0.3s;

					@include bp(md) {
						bottom: -1px;
					}
				}

				&:hover,
				&.active {
					&:before {
						width: calc(100% - 18px);

						@include bp(md) {
							width: 100%;
						}
					}

					.CategoryName:before {
						background: transparent;

						@include bp(md) {
							background: #78746d;
						}
					}
				}

				.CategoryName {
					padding-left: 20px;

					@include bp(md) {
						padding-left: 0px;
					}

					&:before {
						content: '';
						height: 2px;
						width: 2px;
						border: 1px solid #78746d;
						position: absolute;
						transform: rotateZ(45deg);
						top: 22px;
						left: 4px;
						transition: background 0.3s;
						background: #78746d;

						@include bp(md) {
							top: 28px;
							left: -32px;
							height: 4px;
							width: 4px;
							background: transparent;
						}
					}

					&:after {
						content: '';
						height: 8px;
						width: 8px;
						border: 1px solid rgba(#78746d, 0.2);
						position: absolute;
						transform: rotateZ(45deg);
						top: 19px;
						left: 1px;

						@include bp(md) {
							top: 24px;
							left: -36px;
							height: 12px;
							width: 12px;
						}
					}
				}
			}

			.NewsSubCategories {
				list-style: none;
				padding-bottom: 20px;
				display: none;

				@include bp(md) {
					display: block;
				}

				li a {
					font-size: 14px;
					color: rgba($COLOR_TEXT, 0.6);
					letter-spacing: 0.02em;
					padding: 22px 0 0 0;
					text-decoration: none;
					width: 100%;
					display: flex;
					justify-content: space-between;
					flex-wrap: wrap;
					transition: 0.2s color;
				}

				li:hover a,
				li.active a {
					color: rgba($COLOR_TEXT, 1);
				}
			}

			.ArticleCategory {
				display: flex;
				justify-content: space-between;
				flex-wrap: wrap;
			}

			.ExternalCategory {
				display: flex;
				justify-content: space-between;
				flex-wrap: wrap;
			}

			.ArticlesAmount {
				font-family: $font-rogan;
				font-weight: $font-weight-rogan-bold;
				font-size: 12px;
				display: none;

				@include bp(md) {
					display: inline-block;
				}
			}

			.Separator {
				margin-top: 16px;
			}
		}
	}

	.ArticlePreviewHeader {
		margin-top: 72px;

		@include bp(md) {
			margin-top: 0;
		}

		.eyebrow-red {
			margin-bottom: 10px;
		}

		h2 {
			@extend h1;
			max-width: 570px;
			margin-bottom: 56px;

			@include bp(md) {
				margin-bottom: 120px;
			}
		}
	}

	.MobileCategory {
		width: 100%;
		margin-bottom: 24px;
		position: relative;

		@include bp(md) {
			display: none;
		}

		&:after {
			content: '';
			width: 0;
			height: 0;
			border-left: 4px solid transparent;
			border-right: 4px solid transparent;
			border-top: 4px solid #78746d;
			position: absolute;
			right: 0;
			top: 10px;
			transform: rotateZ(0deg);
		}

		select {
			width: 100%;
			-webkit-appearance: none;
			-moz-appearance: none;
			text-indent: 1px;
			text-overflow: '';
			border: 0;
			background: transparent;
			border-bottom: 1px solid rgba(#78746d, 0.2);
			@extend .uppercase;
			font-size: 12px;
			height: 35px;
			outline: none;
			border-radius: 0;
		}

		select::-ms-expand {
			display: none;
		}
	}

	.ArticlesFilter {
		display: flex;
		margin-bottom: 24px;
		align-items: center;

		p,
		button {
			@extend .uppercase;
			font-size: 12px;
		}

		p {
			color: #78746d;
		}

		.desktop {
			display: none;

			@include bp(md) {
				display: inline-block;
			}
		}

		button {
			background: transparent;
			border: none;
			cursor: pointer;
			padding: 0;
			position: relative;
			margin-left: 4px;
			padding-right: 12px;
			width: 100%;
			text-align: left;
			height: 30px;

			@include bp(md) {
				width: 120px;
				height: auto;
			}

			&.up {
				&:after {
					transform: rotateZ(0deg);
				}
			}

			.mobile {
				display: inline-block;

				@include bp(md) {
					display: none;
				}
			}

			&:before {
				content: '';
				position: absolute;
				bottom: -4px;
				left: 0;
				width: 100%;
				border-bottom: 1px solid rgba(#78746d, 0.2);
			}

			&:after {
				content: '';
				width: 0;
				height: 0;
				border-left: 4px solid transparent;
				border-right: 4px solid transparent;
				border-top: 4px solid #78746d;
				position: absolute;
				right: 0;
				top: 10px;
				transform: rotateZ(180deg);

				@include bp(md) {
					top: 4px;
				}
			}
		}
	}

	.articles {
		margin-top: 20px;

		.ArticlePreview {
			width: 100%;
			text-decoration: none;
			padding: 25px 0;
			display: flex;
			flex-direction: column;

			@include bp(sm) {
				flex-direction: row;
				border-bottom: 1px solid rgba(#78746d, 0.2);
			}

			& > * {
				pointer-events: none;
			}

			&:last-child {
				border-bottom: 0;
				padding-bottom: 0;
			}

			.ArticleThumbnail {
				width: 100%;
				flex-shrink: 0;
				@extend .borderedImage;
				margin-bottom: 5px;

				@include bp(sm) {
					width: 280px;
				}
				@include bp(md) {
					width: 316px;
				}
			}

			.imageContainer {
			}

			.ArticlePreviewContent {
				width: 100%;

				@include bp(sm) {
					width: calc(100% - 280px);
					padding-left: 32px;
				}

				@include bp(md) {
					width: calc(100% - 316px);
					padding-left: 32px;
				}

				.ContentTopBar {
					display: flex;
					align-items: center;
					justify-content: space-between;
					margin-bottom: 24px;
					margin-top: 16px;

					@include bp(sm) {
						margin-top: 0;
					}

					p {
						font-family: $font-rogan;
						font-weight: $font-weight-rogan-bold;
						color: rgba($COLOR_TEXT, 0.6);
						letter-spacing: 0.24em;
						text-transform: uppercase;
						line-height: 1.333em;
						font-size: 9px;

						@include bp(md) {
							font-size: 12px;
						}
					}
				}

				.ArticleTitle {
				}
			}
		}
	}

	.PressKit {
		.AboutText {
			max-width: 550px;
			color: $COLOR_GREY;
			line-height: em(28px, 16px);
		}

		.CompanyInfos {
			margin-top: 60px;

			@include bp(sm) {
				display: flex;
			}

			p {
				@extend .uppercase;
				font-size: 12px;

				@include bp(sm) {
					margin-right: 80px;
				}

				&:first-child {
					opacity: 0.6;
					margin-bottom: 4px;

					@include bp(sm) {
						margin-bottom: 0;
					}
				}

				&:last-of-type {
					margin-bottom: 18px;

					@include bp(sm) {
						margin-bottom: 0;
					}
				}
			}
		}

		.PressKitSections {
			margin-top: 70px;

			@include bp(sm) {
				margin-top: 100px;
			}

			.PressKitSection {
				&:nth-child(1) {
					.PressKitSectionHeader {
						border-top: 1px solid rgba(#524d45, 0.2);

						@include bp(sm) {
							border: 0;
						}
					}
				}

				.PressKitSectionHeader {
					display: flex;
					align-items: center;
					justify-content: space-between;
					height: 60px;
					cursor: pointer;

					@include bp(sm) {
						height: 72px;
					}

					.cross {
						transition: transform 0.3s;
						line-height: 0;
					}

					&.toggled {
						.cross {
							transform: rotateZ(45deg);
						}
					}
				}

				.PressKitSectionTitle {
					display: flex;
					@extend .uppercase;
					font-size: 12px;

					span {
						position: relative;
						display: inline-block;
						margin-left: 32px;
						opacity: 0.6;

						&:after {
							height: 2px;
							width: 2px;
							background-color: rgba($COLOR_TEXT, 1);
							opacity: 0.4;
							content: '';
							position: absolute;
							top: calc(50% - 1px);
							left: -18px;
						}
					}
				}

				//.PressKitSectionHeader.toggled + .PressKitSectionBody {
				//	height: 100%;
				//}

				.PressKitSectionBody {
					border-bottom: 1px solid rgba(#524d45, 0.2);
					height: 0px;
					overflow: hidden;
					//transition: height 0.3s;

					.SectionBodyInner {
						padding: 5px;
						display: flex;
						flex-wrap: wrap;
					}

					.DownloadLink {
						text-decoration: none;
						color: $COLOR_GREY;
						display: flex;
						align-items: center;
						padding: 6px 0;
						line-height: 0;
						transition: color 0.3s;
						font-size: 12px;

						@include bp(sm) {
							font-size: 14px;
						}

						&:hover {
							color: $COLOR_TEXT;

							svg g path {
								fill: $COLOR_RED;
							}
						}

						svg {
							width: 12px;
							height: 10px;
							margin-right: 6px;

							g path {
								transition: fill 0.3s;
							}
						}

						span {
							position: relative;
							display: inline-block;
							margin-left: 18px;

							&:after {
								height: 2px;
								width: 2px;
								background-color: $COLOR_GREY;
								content: '';
								position: absolute;
								top: calc(50% - 1px);
								left: -11px;
							}
						}
					}

					.PdfFile {
						height: 60px;
						@extend .dotted-border;
						display: flex;
						align-items: center;
						width: 100%;

						@include bp(sm) {
							height: 80px;
						}

						&:after {
							bottom: auto;
							top: 0;
						}

						a {
							& > * {
								pointer-events: none;
							}

							span {
								// display: none;
								@include bp(sm) {
									display: inline;
								}
							}
						}
					}

					.FilesSection {
						width: 100%;

						.FilesSectionBody {
							height: 0px;
							overflow: hidden;
							margin-left: 26px;
						}

						.FilesSectionHeader {
							@extend .dotted-border;
							display: flex;
							align-items: center;
							justify-content: flex-start;
							height: 60px;
							cursor: pointer;

							&:after {
								bottom: auto;
								top: 0;
							}

							@include bp(sm) {
								height: 80px;
							}

							.FilesSectionTitle {
								text-decoration: none;
								color: $COLOR_GREY;
								display: flex;
								align-items: center;
								padding: 6px 0;
								line-height: 0;
								transition: color 0.3s;
								font-size: 12px;

								@include bp(sm) {
									font-size: 14px;
								}

								span {
									position: relative;
									display: inline-block;
									margin-left: 18px;

									&:after {
										height: 2px;
										width: 2px;
										background-color: $COLOR_GREY;
										content: '';
										position: absolute;
										top: calc(50% - 1px);
										left: -11px;
									}
								}
							}

							.cross {
								transition: transform 0.3s;
								line-height: 0;
							}

							svg {
								width: 12px;
								height: 8px;
								margin-right: 6px;

								g path {
									transition: fill 0.3s;
								}
							}

							&.toggled {
								.cross {
									transform: rotateZ(45deg);
								}
							}

							&:hover {
								.FilesSectionTitle {
									color: $COLOR_TEXT;
								}

								svg g path {
									fill: $COLOR_RED;
								}
							}
						}
					}

					.ImgFile {
						width: calc((100% - 24px) / 2);
						margin-bottom: 36px;

						&:nth-child(2n + 1) {
							margin-right: 24px;
						}

						@include bp(sm) {
							width: calc((100% - 64px) / 3);

							&:nth-child(2n + 1) {
								margin-right: 0;
							}

							&:nth-child(3n + 2) {
								margin: 0 32px;
							}
						}

						.imageContainer {
							display: block;
							text-decoration: none;
						}

						.DownloadLink {
							display: inline-block;
							margin-top: 12px;

							@include bp(sm) {
								margin-top: 24px;
							}

							span {
								display: none;
								@include bp(sm) {
									display: inline;
								}
							}
						}
					}

					.FileInfo {
						text-transform: uppercase;
					}
				}
			}
		}
	}
}
