@keyframes fade-down-up {
	0% {
		opacity: 1;
	}
	50% {
		opacity: 0.2;
	}
	100% {
		opacity: 1;
	}
}
.ContactModule {
	.errors {
		color: $COLOR_RED;
		position: absolute;
		bottom: 85px;
		width: 100%;
		max-width: 280px;
		font-size: 11px;

		span {
			opacity: 0;
			text-align: left;
			transition: opacity 0.4s;
			display: block;
			position: absolute;
			top: 0;
			width: 100%;
			&.active {
				opacity: 1;
			}
		}
	}
	.formWrapper {
		transition: opacity 0.4s;
	}
	&.submitting {
		.formWrapper {
			animation: fade-down-up infinite 0.75s;
			pointer-events: none;
		}
	}
	&.submitted {
		.formWrapper {
			animation: none;
			opacity: 0.2;
			pointer-events: none;
		}
	}
	.moduleInner {
		padding-top: 150px;
		padding-bottom: 90px;

		@include bp(sm) {
			padding-top: 300px;
			padding-bottom: 200px;
		}
	}

	.eyebrow-red {
		margin-bottom: 12px;

		@include bp(sm) {
			margin-bottom: 20px;
		}
	}

	h1 {
		max-width: 200px;

		@include bp(sm) {
			max-width: 100%;
		}
	}

	.text-14 {
		color: $COLOR_GREY;
	}

	.text-16 {
		opacity: 0.7;
		margin-top: 16px;
		margin-bottom: 60px;
	}

	.TextWrapper {
		position: relative;

		.ValidationText {
			position: absolute;
			left: 0;
			top: 0;

			h1,
			p {
				opacity: 0;
			}
		}
	}

	.Form {
		display: flex;
		flex-wrap: wrap;
	}

	.FormLine {
		width: 100%;

		@include bp(sm) {
			margin: 0 -16px;
			display: flex;
			flex-grow: 1;
			align-items: flex-start;
		}

		.half {
			@include bp(sm) {
				margin-left: 16px;
				margin-right: 16px;
			}
		}
	}

	.FormControl {
		position: relative;
		width: 100%;
		line-height: 0;
		display: flex;
		align-items: center;
		margin-bottom: 24px;
		flex-wrap: wrap;

		.ErrorMessage {
			position: absolute;
			bottom: -22px;
			left: 5px;
			color: $COLOR_RED;
			opacity: 0;
			line-height: 1em;
		}

		&.InputControl,
		&.SelectControl,
		&.TextareaControl {
			&:before {
				content: '';
				height: calc(100% - 8px);
				width: calc(100% - 8px);
				border: 1px solid rgba($COLOR_BEIGE_DARK, 0.2);
				box-sizing: border-box;
				position: absolute;
				top: 4px;
				left: 4px;
				pointer-events: none;
			}

			&.error {
				margin-bottom: 50px;

				input[type='text'],
				input[type='email'],
				select,
				textarea {
					border-color: rgba($COLOR_RED, 0.3);
				}

				&:before {
					border-color: rgba($COLOR_RED, 0.5);
				}

				.ErrorMessage {
					opacity: 1;
				}
			}
		}

		&.TextareaControl {
			//margin-bottom: 0;
			position: relative;
			//margin-top: 40px;

			.disclaimer {
				position: absolute;
				top: -36px;
				left: 5px;
			}
		}

		&.CheckboxControl {
			display: flex;
			align-items: center;
			line-height: 0;
			position: relative;
			padding-left: 32px;
			margin-top: 0px;

			//@include bp(sm) {
			//	margin-top: 0;
			//}

			label {
				line-height: 20px;
				cursor: pointer;
				color: #797672;

				@include bp(sm) {
					height: 16px;
				}
			}

			input {
				opacity: 0;
				position: absolute;
				left: 0;
				top: 0;
			}

			.checkbox {
				position: absolute;
				left: 0;
				top: 2px;
				height: 18px;
				width: 18px;
				//border: 1px solid;

				@include bp(sm) {
					left: 0;
					top: 0;
				}

				&:before {
					content: '';
					position: absolute;
					height: 16px;
					width: 16px;
					border: 1px solid rgba($COLOR_BEIGE_DARK, 0.2);
					left: 0px;
					top: 0px;
				}

				&:after {
					content: '';
					height: 12px;
					width: 12px;
					background: rgba($COLOR_BEIGE_DARK, 0.4);
					top: 3px;
					left: 3px;
					position: absolute;
					opacity: 0;
				}
			}

			.ErrorMessage {
				bottom: -30px;

				@include bp(sm) {
					bottom: -22px;
				}
			}

			&.error {
				.checkbox:before {
					border-color: rgba($COLOR_RED, 0.4);
				}

				.ErrorMessage {
					opacity: 1;
				}
			}

			input:checked + .checkbox:after {
				opacity: 1;
			}

			a {
				line-height: 20px;
			}
		}

		&.half {
			width: 100%;

			@include bp(sm) {
				width: 50%;
			}
		}

		input[type='text'],
		input[type='email'],
		select,
		textarea {
			width: 100%;
			background: transparent;
			border: 1px solid rgba($COLOR_BEIGE_DARK, 0.2);
			box-sizing: border-box;
			font-size: 12px;
			line-height: em(16px, 12px);
			font-family: $FONT;
			letter-spacing: 0.04em;
			outline: none;
			border-radius: 0;
			-webkit-appearance: none;

			@include bp(sm) {
				font-size: 14px;
			}
		}

		input[type='text'],
		input[type='email'],
		select {
			height: 62px;
			padding: 0 20px;
		}

		textarea {
			resize: none;
			padding: 24px;
			height: 245px;
		}

		select::-ms-expand {
			display: none;
		}

		select {
			-webkit-appearance: none;
			-moz-appearance: none;
			text-indent: 1px;
			text-overflow: '';
		}

		.SelectIcon {
			position: absolute;
			right: 30px;
			top: calc(50% - 3px);
			width: 10px;

			svg {
				width: 10px;
				height: 7px;
			}
		}
	}

	.ButtonCta {
		margin-top: 24px;
		display: inline-block;
		position: relative;
		padding-right: 66px;
		padding-left: 30px;

		&:before {
			position: absolute;
			content: '';
			top: calc(50% - 8px);
			right: 32px;
			height: 16px;
			width: 25px;
			background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg width='16px' height='9px' viewBox='0 0 16 9' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Ctitle%3EGroup 2%3C/title%3E%3Cg id='Symbols' stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3E%3Cg id='Group' transform='translate(9.292893, 4.535534) scale(-1, 1) translate(-9.292893, -4.535534) translate(-0.000000, 1.000000)'%3E%3Cg id='Group-2' transform='translate(3.585786, 0.000000)'%3E%3Cpolygon id='Rectangle-Copy-4' stroke='%23FA3B02' transform='translate(3.535534, 3.535534) rotate(-315.000000) translate(-3.535534, -3.535534) ' points='1.03553391 1.03553391 6.03553391 1.03553391 6.03553391 6.03553391 1.03553391 6.03553391'%3E%3C/polygon%3E%3Crect id='Rectangle' fill='%23FA3B02' transform='translate(11.000000, 3.500000) scale(-1, 1) translate(-11.000000, -3.500000) ' x='7' y='3' width='8' height='1'%3E%3C/rect%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
			background-repeat: no-repeat;
			background-size: contain;
			transition: transform 0.3s;
		}

		&:after {
			content: '';
			height: 6px;
			width: 6px;
			border: 1px solid #f53a02;
			position: absolute;
			box-sizing: border-box;
			right: 30px;
			top: calc(50% - 4px);
			transform: rotateZ(45deg);
			transition: transform 0.3s;
			opacity: 0.4;
		}

		&:hover {
			&:before {
				transform: translateX(6px);
			}
		}
	}
}
