.ShowroomHoverButton {
	position: absolute;
	bottom: -110px;
	left: 0;
	width: 80px;
	height: 80px;
	cursor: pointer;
	pointer-events: none;
	transform: translate3d(0, 0, 10px);
	will-change: transform;
	//visibility: hidden;
	@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
		bottom: -120px;
		left: calc(50% - 40px);
		width: 100px;
		height: 100px;
	}

	.ShowroomHoverButton-outer {
		position: absolute;
		top: calc(50% - 40px);
		left: calc(50% - 40px);
		border: 2px solid #fa3b02;
		border-radius: 50%;
		width: 80px;
		height: 80px;
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
			top: calc(50% - 50px);
			left: calc(50% - 50px);
			width: 100px;
			height: 100px;
		}
	}

	.ShowroomHoverButton-inner {
		position: absolute;
		border: 1px solid rgba(#fa3b02, 0.2);
		border-radius: 50%;

		width: 66px;
		height: 66px;
		top: calc(50% - 33px);
		left: calc(50% - 33px);
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
			width: 86px;
			height: 86px;
			top: calc(50% - 43px);
			left: calc(50% - 43px);
		}
		img {
			width: 60% !important;
			height: 60% !important;
			top: calc(20% + 1px) !important;
			left: calc(20% + 1px) !important;
			object-fit: contain;
			opacity: 1 !important;
			border: none;
			outline: none;
		}
	}

	.ShowroomHoverButton-line {
		width: 1px;
		background: #fa3b02;
		position: absolute;
		top: 50%;
		left: 50%;
		transform-origin: 0 0;
		transform: rotateZ(210deg) translateY(27px);
		height: 60px;
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
			transform: rotateZ(170deg) translateY(37px);
			height: 80px;
		}
	}

	.ShowroomHoverButton-lineDot {
		position: absolute;
		border: 1px solid #fa3b02;
		border-radius: 50%;
		width: 6px;
		height: 6px;
		top: 50%;
		left: 50%;
		transform-origin: 0 0;
		transform: rotateZ(210deg) translateY(27px + 59px) translateX(-2.5px);
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
			transform: rotateZ(170deg) translateY(37px + 79px) translateX(-2.5px);
		}
	}

	.ShowroomHoverButton-label {
		font-family: $font-roobert;
		color: #231f20;
		font-weight: $font-weight-roobert-regular;
		text-align: left;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 120%;
		width: 300px;
		display: flex;
		flex-flow: column;
		justify-content: center;
		//transform-origin: 0 0;
		span {
			letter-spacing: 0.03em;
		}
		//text-shadow: 0px 0px 1px $COLOR_BEIGE;
		span:nth-child(1) {
			font-size: 15px;
			@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
				font-size: 18px;
			}
		}
		span:nth-child(2) {
			color: #78746d;
			font-size: 15px;
			@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
				font-size: 18px;
			}
		}
	}
}
