#LandingGallery {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	flex-flow: column;
	justify-content: flex-end;
	visibility: hidden;
	opacity: 0;
	background: #000000;
	padding: 35px;
	z-index: 15;
	@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) {
		padding: 60px;
	}

	&:before {
		content: '';
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		height: 20%;
		max-height: 265px;
		z-index: 1;
		opacity: 0.3;
		background-image: linear-gradient(180deg, rgba(20, 19, 19, 1) 36%, rgba(20, 19, 19, 0) 100%);
	}

	.LandingGallery-nav {
		position: absolute;
		top: 0;
		left: 0;
		width: 20%;
		max-width: 200px;
		height: 100%;
		//padding-top: 30%;
		z-index: 3;

		span {
			font-size: 11px;
			font-family: $font-rogan;
			color: rgb(255, 255, 255);
			letter-spacing: 0.26em;
			font-weight: $font-weight-rogan-bold;
			text-transform: uppercase;
		}
		@media (hover: hover) {
			&:hover {
				& > div {
					transform: translate3d(50px, 0, 0);
				}

				&.next > div {
					transform: translate3d(-50px, 0, 0);
				}
			}
		}
		& > div {
			position: absolute;
			left: 0;
			top: 50%;
			display: flex;
			flex-flow: row;
			flex-shrink: 0;
			justify-content: center;
			align-items: center;
			width: 80px;
			transform: translate3d(0px, 0, 0);
			transition: $ease-out-quad 0.3s;

			&:before {
				content: '';
				width: 60px;
				height: 2px;
				left: -62px;
				position: absolute;
				display: block;
				background-image: url("data:image/svg+xml,%3Csvg width='2' height='2' viewBox='0 0 6 2' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='1' cy='1' r='1' fill='%23ffffff'/%3E%3C/svg%3E%0A");
				background-size: 6px 2px;
				background-repeat: repeat-x;
			}

			span {
				display: block;
				width: 42px;
				flex-shrink: 0;
				text-align: right;
			}

			div.LandingGallery-diamond {
				flex-shrink: 0;
				width: 24px;
				height: 24px;
				line-height: 0;
				position: relative;
				margin-top: 14px;
				margin-bottom: -14px;
				transform: rotateZ(45deg);

				&:before {
					content: '';
					width: 11px;
					height: 11px;
					display: block;
					position: absolute;
					box-sizing: content-box;
					top: -4px;
					left: -4px;
					@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
						width: 15px;
						height: 15px;
						top: -6px;
						left: -6px;
					}

					border: 1px solid rgba(255, 255, 255, 0.4);
				}

				&:after {
					content: '';
					width: 5px;
					height: 5px;
					background: #ffffff;
					display: block;
					position: absolute;
					box-sizing: content-box;
					top: 0px;
					left: 0px;
					will-change: transform;
					transition: 0.3s;
				}
			}
		}

		&.next {
			left: auto;
			right: 0;

			& > div {
				left: auto;
				right: 0;

				&:before {
					right: -62px;
					left: auto;
				}
			}

			span {
				text-align: left;
			}
		}
	}

	.LandingGallery-jumpNav {
		display: flex;
		flex-flow: row;
		justify-content: center;
		align-items: center;
		position: absolute;
		width: 100vw;
		left: calc(50% - 50vw);
		margin-bottom: -10px;
		@media (min-width: $BREAKPOINT_LANDING_TABLET_LANDSCAPE) {
			margin-bottom: 0px;
			width: 320px;
			left: calc(50% - 160px);
		}

		a {
			flex-shrink: 0;
			width: 30px;
			height: 30px;
			line-height: 0;
			position: relative;
			margin-top: 14px;
			margin-bottom: -14px;
			display: block;
			opacity: 0.4;
			transition: opacity 0.2s;
			@media (min-width: $BREAKPOINT_LANDING_TABLET_LANDSCAPE) {
				width: 40px;
				height: 40px;
			}

			&.active {
				opacity: 1;
			}

			&:before {
				content: '';
				width: 10px;
				height: 10px;
				display: block;
				position: absolute;
				box-sizing: content-box;
				top: calc(50% - 6px);
				left: calc(50% - 6px);
				transform: rotateZ(45deg);

				@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) {
					width: 12px;
					height: 12px;
					top: calc(50% - 7px);
					left: calc(50% - 7px);
				}

				border: 1px solid rgba(255, 255, 255, 0.2);
			}

			&:after {
				content: '';
				background: #ffffff;
				display: block;
				position: absolute;
				box-sizing: content-box;
				width: 4px;
				height: 4px;
				top: calc(50% - 2px);
				left: calc(50% - 2px);
				transform: rotateZ(45deg);
				will-change: transform;
				transition: 0.3s;
			}
		}
	}

	.LandingGallery-images {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		display: flex;
		overflow: hidden;
		flex: 1;

		img {
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			object-fit: cover;
			opacity: 0;
			transition: opacity 0.2s;

			&.loaded {
				opacity: 1;
			}
		}

		.LandingGallery-imagesInner {
			position: relative;
			height: 100%;
			width: 100%;
			overflow: hidden;
		}
	}

	.LandingGallery-description {
		max-width: 400px;
		margin-bottom: 10px;
		@media (min-width: $BREAKPOINT_LANDING_TABLET_LANDSCAPE) {
			margin-bottom: 0;
		}
		pointer-events: none;
		&:before {
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			max-height: 50vh;
			height: 100%;
			background-image: linear-gradient(180deg, rgba(20, 19, 19, 0) 0%, #141313 100%);
		}

		p {
			opacity: 0.72;
			font-family: $font-roobert;
			font-weight: $font-weight-roobert-regular;
			font-size: 15px;
			color: #ffffff;
			letter-spacing: 0.2px;
			line-height: 22px;
			margin-top: 16px;
			@include bp(md) {
				margin-top: 25px;
				font-size: 16px;
				line-height: 24px;
			}
		}

		h3 {
			position: relative;
			z-index: 1;
			font-family: $font-roobert;
			font-weight: $font-weight-roobert-regular;
			font-size: 20px;
			color: #ffffff;
			letter-spacing: 0;
			line-height: 28px;
			@include bp(md) {
				font-size: 22px;
				line-height: 30px;
			}
		}
	}

	.LandingGallery-close {
		position: absolute;
		top: 40px;
		right: 40px;
		height: 10px;
		width: 10px;
		z-index: 8;
		cursor: pointer;
		line-height: 0;
		@include bp(md) {
			top: 80px;
			right: 80px;
		}

		&:before,
		&:after {
			position: absolute;
			content: '';
			border: 2px solid $COLOR_ORANGE;
			transition: transform 0.5s;
			border-radius: 50%;
		}

		&:before {
			height: 60px;
			width: 60px;
			border: 1px solid rgba($COLOR_ORANGE, 0.3);
			left: -26px;
			top: -26px;
			transition: transform 0.2s;
		}

		&:after {
			height: 72px;
			width: 72px;
			left: -33px;
			top: -33px;
		}

		@media (hover: hover) {
			&:hover {
				&:before {
					transform: scale(1.2, 1.2);
				}

				svg {
					transform: scale(1.2, 1.2);
				}
			}
		}

		svg {
			width: 100%;
			transition: transform 0.2s 0.05s;
		}
	}

	&.single {
		.LandingGallery-nav,
		.LandingGallery-jumpNav {
			display: none;
		}
	}
}
