.ErrorTemplate {
	display: flex;
	flex-flow: column;
	align-items: center;
	& > div.container {
		margin: auto auto;
	}
	.content {
		display: flex;
		flex-flow: column;
		align-items: center;
		p {
			text-align: center;
			font-size: 18px;
			font-family: $FONT;
		}
		.ButtonCta {
			margin-top: 50px;
			margin-bottom: 30px;
		}
		.status {
			margin-bottom: 50px;
			margin-top: 50px;
			font-style: normal;
			font-weight: normal;
			font-size: 40px;
			line-height: 46px;
			letter-spacing: 1px;
			text-transform: uppercase;
			text-align: center;
		}

	}
}
