.MakeChoice {
	position: absolute;
	left: 0;
	right: 0;
	top: 80px;
	bottom: 80px;
	visibility: hidden;
	@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
		top: 130px;
		bottom: 130px;
		left: 35px;
		right: 35px;
	}
	@media (min-width: $BREAKPOINT_LANDING_TABLET_LANDSCAPE) {
		top: 130px;
		bottom: 130px;
		left: 18px;
		right: 18px;
	}
	@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: $BREAKPOINT_HEIGHT) {
		top: 130px;
		bottom: 130px;
		left: 51px;
		right: 51px;
	}
	display: flex;
	flex-flow: column;
	align-items: center;
	justify-content: center;
	user-select: none;
	pointer-events: none;
	@media (min-width: $BREAKPOINT_LANDING_TABLET_LANDSCAPE) {
		flex-flow: row;
	}

	.MakeChoice-button {
		cursor: pointer;
		top: 0;
		left: 0;
		width: calc((132 / 780) * 100%);
		height: calc((132 / 780) * 100%);
		border-radius: 50%;
		display: block;
		position: absolute;
		box-sizing: border-box;
		line-height: 0;
		opacity: 0;
		@media (min-width: 560px) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
			width: calc((80 / 780) * 100%);
			height: calc((80 / 780) * 100%);
		}

		&:before {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate3d(-50%, -50%, 0px);
			border-radius: 50%;
			width: percentage(31/38);
			height: percentage(31/38);
			display: block;
			border: 1px solid #000000;
			box-sizing: border-box;
			@media (min-width: 560px) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
				border: 2px solid #000000;
			}
		}

		&:after {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate3d(-50%, -50%, 0px);
			border-radius: 50%;
			width: 100%;
			height: 100%;
			box-sizing: border-box;
			display: block;
			border: 1px solid #bdb8b0;
		}

		svg {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate3d(-50%, -50%, 0px);
			width: 20%;
			height: 20%;
		}

		&:nth-of-type(2) {
			left: calc(50% - (((132 * 0.5) / 780) * 100%));
			top: calc((-12 / 780) * 100%);
			@media (min-width: 560px) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
				left: calc(50% - ((40 / 780) * 100%));
				top: calc((19 / 780) * 100%);
			}
		}

		&:nth-of-type(4) {
			top: calc((444 / 780) * 100%);
			left: calc((24 / 780) * 100%);
			@media (min-width: 560px) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
				top: calc((467 / 780) * 100%);
				left: calc((45 / 780) * 100%);
			}
		}

		&:nth-of-type(3) {
			top: calc((444 / 780) * 100%);
			left: calc((626 / 780) * 100%);
			@media (min-width: 560px) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
				top: calc((467 / 780) * 100%);
				left: calc((655 / 780) * 100%);
			}
		}
	}

	.MakeChoice-svgWrapper {
		padding-top: 100%;
		position: relative;
	}

	.MakeChoice-bgSVG {
		position: absolute;
		top: 0;
		left: 0;
		transform: translate3d(0px, 0px, 0px);
		width: 100%;
		height: 100%;
		background-image: url("data:image/svg+xml,%3Csvg class='MakeChoice-bgSVG' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' viewBox='0 0 780 780'%3E%3Cdefs%3E%3Cpath d='M390 0c215.391 0 390 174.609 390 390S605.391 780 390 780 0 605.391 0 390 174.609 0 390 0zm299.363 456.839c-28.719 0-52 23.281-52 52s23.281 52 52 52 52-23.281 52-52-23.281-52-52-52zm-603.585-.145c-28.719 0-52 23.281-52 52s23.281 52 52 52c28.72 0 52-23.281 52-52s-23.28-52-52-52zM390.143 8.984c-27.062 0-49 21.939-49 49 0 27.063 21.938 49 49 49s49-21.937 49-49c0-27.061-21.938-49-49-49z' id='a'/%3E%3C/defs%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cmask id='b' fill='%23fff'%3E%3Cuse xlink:href='%23a'/%3E%3C/mask%3E%3Cg stroke='%23A89E9E' stroke-width='2' mask='url(%23b)' stroke-dasharray='0,8' stroke-linecap='round'%3E%3Cpath d='M390 723c185.015 0 335-149.985 335-335S575.015 53 390 53 55 202.985 55 388s149.985 335 335 335z'/%3E%3Cpath d='M390 710c177.836 0 322-144.164 322-322S567.836 66 390 66 68 210.164 68 388s144.164 322 322 322z'/%3E%3Cpath d='M89.259 500.748L386.853 63.856a4 4 0 016.613.002L690.747 500.75a4 4 0 01-3.307 6.25H92.564a4 4 0 01-3.305-6.252z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
		will-change: opacity;
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}
	}

	.MakeChoice-scrollIndicator {
		position: absolute;
		left: 14px;
		left: calc(14px + env(safe-area-inset-left));
		height: 300px;
		max-height: calc(100vh - 200px);
		@media (min-width: $BREAKPOINT_LANDING_TABLET_LANDSCAPE) and (min-height: $BREAKPOINT_HEIGHT) {
			left: 22px;
			left: calc(22px + env(safe-area-inset-left));
		}
		.MakeChoice-scrollIndicator-lineBG {
			content: '';
			position: absolute;
			height: 100%;
			top: 0px;
			width: 2px;
			opacity: 0.7;
			background-image: url("data:image/svg+xml,%3Csvg width='2' height='2' viewBox='0 0 2 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='1' cy='1' r='1' fill='%2378746D'/%3E%3C/svg%3E%0A");
			@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
				opacity: 1;
				background-image: url("data:image/svg+xml,%3Csvg width='2' height='2' viewBox='0 0 2 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='1' cy='1' r='1' fill='%2388837a'/%3E%3C/svg%3E%0A");
			}
			background-size: 2px 6px;
			background-repeat: repeat-y;
			transform-origin: center top;
			will-change: transform;
		}

		.MakeChoice-scrollIndicator-line {
			transform-origin: center top;
			position: absolute;
			top: -6px;
			width: 2px;
			height: calc(100% + 2px);
			background: $COLOR_ORANGE;
			transform: scaleY(0);
			will-change: transform;
		}

		.MakeChoice-Diamond {
			position: absolute;
			left: -4px;
			top: -15px;
			width: 10px;
			height: 10px;
			display: block;
			transform: rotateZ(45deg);
			border: 2px solid $COLOR_ORANGE;

			&:last-child {
				top: auto;
				bottom: -11px;
				border: 2px solid rgba(51, 47, 42, 0.2);
			}
		}
	}

	.MakeChoice-text {
		text-align: center;
		max-width: calc(100vw - 40px);
		@media (min-width: $BREAKPOINT_LANDING_TABLET_LANDSCAPE) {
			position: absolute;
			left: 59px;
			text-align: left;
		}

		br {
			display: none;
			@media (min-width: $BREAKPOINT_LANDING_TABLET_LANDSCAPE) {
				display: inline;
			}
		}

		h2 {
			font-family: $font-roobert;
			font-weight: $font-weight-roobert-regular;
			color: #231f20;
			letter-spacing: 0.02em;
			font-size: 16px;
			line-height: 24px;
			@media (max-aspect-ratio: 1/1) and (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
				font-size: 22px;
				line-height: 30px;
				letter-spacing: 0;
			}
			@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: $BREAKPOINT_HEIGHT) {
				font-size: 22px;
				line-height: 30px;
				letter-spacing: 0;
			}
		}

		p {
			font-family: $font-roobert;
			font-weight: $font-weight-roobert-regular;
			font-size: 15px;
			color: #78746d;
			letter-spacing: 0.02em;
			line-height: 22px;
			@media (max-aspect-ratio: 1/1) and (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
				font-size: 16px;
				line-height: 24px;
				margin-top: 14px;
			}
			@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: $BREAKPOINT_HEIGHT) {
				font-size: 16px;
				line-height: 24px;
				margin-top: 14px;
			}
		}
	}

	.MakeChoice-center {
		position: relative;
		line-height: 0;
		max-height: 90vh;
		height: 85vw;
		width: calc(100% - 30px);
		display: flex;
		flex-flow: column;
		justify-content: center;
		margin-top: 30px;
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
			margin-top: 0;
			max-height: 90vh;
			height: 85vw;
			width: calc(100% - 50px);
			max-width: calc(100vw - 200px);
		}
		@media (min-width: $BREAKPOINT_LANDING_TABLET_LANDSCAPE) and (min-height: $BREAKPOINT_HEIGHT) {
			height: 80vw;
			width: calc(100% - 500px);
		}
		@media (min-aspect-ratio: 1/1) {
			max-height: 70vh;
			height: 70vh;
			width: 70vh;
		}
		@media (min-width: $BREAKPOINT_LANDING_TABLET_LANDSCAPE) {
			max-height: 90vh;
			height: 85vh;
			width: 85vh;
			max-width: calc(100vw - 600px);
		}
	}

	.MakeChoice-images {
		position: absolute;
		top: 5%;
		left: 5%;
		width: 90%;
		height: 90%;
		object-fit: contain;

		img {
			will-change: transform;
			object-fit: contain;
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			z-index: 1;
		}

		.MakeChoice-imageSingular {
			opacity: 0;
			top: 3.6%;
			width: 77.07%;
			left: 12.08%;
			z-index: 0;
		}

		&.MakeChoice-ships {
			img {
				width: percentage(1524/1600 * 0.95);
				height: percentage(1348/1600 * 0.95);
				left: percentage((1 - 1524/1600) * 0.5 + 0.025);
				top: percentage((1 - 1348/1600) * 0.5 + 0);
			}

			.MakeChoice-imageSingular {
				width: percentage(1576/1600 * 0.77);
				height: percentage(1218/1600 * 0.77);
				left: percentage((1 - 1576/1600) * 0.5 * 0.77 + 0.123);
				top: percentage((1 - 1218/1600) * 0.5 * 0.77 + 0.076);
			}
		}
	}

	.MakeChoice-descriptions {
		position: relative;
		right: 0;
		left: 0;
		width: 100%;
		display: flex;
		flex-flow: column;
		justify-content: center;
		align-items: center;
		@media (min-width: $BREAKPOINT_LANDING_TABLET_LANDSCAPE) {
			right: 185px;
			left: auto;
			width: auto;
			display: block;
			position: absolute;
		}
		@media (min-aspect-ratio: 17/11) and (min-width: 1440px) {
			position: relative;
			right: -185px;
		}

		.MakeChoice-description {
			position: absolute;
			top: 0px;
			height: auto;
			width: 100%;
			display: flex;
			flex-flow: column;
			justify-content: center;
			text-align: center;
			opacity: 0;
			max-width: 240px;

			p {
				br {
					display: none;
					@media (min-width: $BREAKPOINT_LANDING_TABLET_LANDSCAPE) {
						display: unset;
					}
				}
			}

			@media (min-width: $BREAKPOINT_LANDING_TABLET_LANDSCAPE) {
				top: -50px;
				left: 0;
				height: 100px;
				width: 185px;
				text-align: right;
			}

			p {
				position: relative;
				@media (min-width: $BREAKPOINT_LANDING_TABLET_LANDSCAPE) {
					margin-top: 10px;
					padding-top: 10px;
					&:before {
						content: '';
						height: 2px;
						width: 100%;
						position: absolute;
						top: 0;
						right: 6px;
						background: rgb(216, 212, 206);
						background: linear-gradient(86deg, rgba(216, 212, 206, 0) 0%, rgba(216, 212, 206, 1) 100%);
					}

					&:after {
						content: '';
						height: 6px;
						width: 6px;
						position: absolute;
						top: -2px;
						right: 0px;
						box-sizing: border-box;
						border-radius: 50%;
						border: 1px solid rgb(216, 212, 206);
					}
				}

				display: block;
				font-size: 14px;
				font-family: $font-roobert;
				font-weight: $font-weight-roobert-regular;
				color: #78746d;
				line-height: 1.429;
				letter-spacing: 0.04em;
			}

			h3 {
				font-size: 14px;
				font-family: $font-roobert;
				font-weight: $font-weight-roobert-regular;
				color: #39352d;
				line-height: 1.429;
				letter-spacing: 0.04em;
			}
		}
	}
}
body.loaded {
	.MakeChoice-button {
		transition: 0.15s;
		transition-timing-function: $ease-out-quad;
		will-change: transform;
		visibility: inherit;
		&:after,
		&:before {
			transition: 0.3s;
			transition-timing-function: $ease-out-quad;
		}

		&:hover {
			animation: none;
			transition-duration: 0.9s;
			transition-timing-function: $ease-out-quad;

			&:after {
				transform: translate3d(-50%, -50%, 0px) scale(1.05);
			}

			&:before {
				transform: translate3d(-50%, -50%, 0px) scale(1.1);
			}

			&:after,
			&:before {
				transition: 0.3s;
				transition-timing-function: $ease-out-quad;
				border-color: $COLOR_ORANGE;
			}
		}
	}
}
