.SectionTitle {
	font-size: 44px;
	font-family: $font-roobert;
	font-weight: $font-weight-roobert-light;
	line-height: em(56px, 44px);
}

.TeamModule {
	.moduleInner {
		min-height: 100vh;
		padding-bottom: 130px;
		padding-top: 120px;

		@include bp(sm) {
			padding-top: 200px;
			padding-bottom: 150px;
		}

		@include bp(md) {
			padding-top: 300px;
			padding-bottom: 200px;
		}
	}

	.SectionLabel {
		margin-bottom: 24px;
	}

	.PageHeader {
		margin-bottom: 70px;

		@include bp(sm) {
			margin-bottom: 100px;
		}

		@include bp(md) {
			margin-bottom: 200px;
		}

		.LinkToOpenPositions {
			font-family: $font-rogan;
			font-weight: $font-weight-rogan-bold;
			font-size: 12px;
			line-height: em(16px, 12px);
			margin-top: 70px;
			color: $COLOR_TEXT;
			display: block;
			text-transform: uppercase;
			letter-spacing: 0.24em;
			position: relative;
			padding-left: 22px;

			@include bp(sm) {
				margin-top: 45px;
			}

			&:before {
				position: absolute;
				content: '';
				top: calc(50% - 4px);
				left: -23px;
				height: 16px;
				width: 25px;
				background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg width='16px' height='9px' viewBox='0 0 16 9' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Ctitle%3EGroup 2%3C/title%3E%3Cg id='Symbols' stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3E%3Cg id='Group' transform='translate(9.292893, 4.535534) scale(-1, 1) translate(-9.292893, -4.535534) translate(-0.000000, 1.000000)'%3E%3Cg id='Group-2' transform='translate(3.585786, 0.000000)'%3E%3Cpolygon id='Rectangle-Copy-4' stroke='%23FA3B02' transform='translate(3.535534, 3.535534) rotate(-315.000000) translate(-3.535534, -3.535534) ' points='1.03553391 1.03553391 6.03553391 1.03553391 6.03553391 6.03553391 1.03553391 6.03553391'%3E%3C/polygon%3E%3Crect id='Rectangle' fill='%23FA3B02' transform='translate(11.000000, 3.500000) scale(-1, 1) translate(-11.000000, -3.500000) ' x='7' y='3' width='8' height='1'%3E%3C/rect%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
				background-repeat: no-repeat;
				background-size: contain;
				transition: transform 0.3s;
				transform-origin: center right;
				transform: rotateZ(90deg);
			}

			&:after {
				content: '';
				height: 6px;
				width: 6px;
				border: 1px solid #f53a02;
				position: absolute;
				box-sizing: border-box;
				left: 0px;
				top: calc(50%);
				transform: rotateZ(45deg);
				transition: transform 0.3s;
				opacity: 0.4;
			}

			@include bp(md) {
				padding-left: 0;

				&:before {
					left: -41px;
				}

				&:after {
					left: -18px;
				}

				&:hover {
					&:before {
						transform: translateY(6px) rotateZ(90deg);
					}
				}
			}
		}
	}

	.TeamSection {
		padding-bottom: 24px;

		@include bp(sm) {
			padding-bottom: 50px;
		}

		@include bp(md) {
			padding-bottom: 80px;
		}
	}

	.TeamPerDepartment {
		margin-bottom: 30px;

		@include bp(md) {
			margin-bottom: 50px;
		}

		.ListItem > * {
			pointer-events: none;
		}

		.DepartmentTitle {
			font-size: 9px;
			font-family: $font-rogan;
			font-weight: $font-weight-rogan-bold;
			text-transform: uppercase;
			letter-spacing: 0.24em;
			width: 100%;
			padding: 15px 0;
			border-bottom: 1px solid rgba(#867f80, 0.2);
			position: relative;
			padding-left: 16px;

			@include bp(md) {
				font-size: 12px;
				padding-left: 0;
			}

			&:before {
				content: '';
				height: 4px;
				width: 4px;
				transform: rotateZ(45deg);
				border: 1px solid $COLOR_TEXT;
				position: absolute;
				left: 0;
				top: calc(50% - 4px);

				@include bp(md) {
					left: -24px;
					top: calc(50% - 3px);
				}
			}
		}
	}

	.OpenPositionsTitle {
		.Separator {
			margin-bottom: 60px;

			@include bp(sm) {
				margin-bottom: 90px;
			}

			@include bp(md) {
				margin-bottom: 130px;
			}
		}
	}

	.OpenPositions {
		margin-top: 48px;

		@include bp(md) {
			margin-top: 100px;
		}

		.ListItemName {
			min-width: 250px;
			@include bp(md) {
				width: 50%;
			}
		}

		.ListItemPosition {
			@include bp(md) {
				width: 25%;
			}
		}

		.ListItemLocation {
			color: #494444;
			margin-top: 4px;
			font-weight: 300;

			@include bp(md) {
				width: 20%;
				padding-left: 25px;
				margin-top: 0;
			}
		}

		.DescriptionText {
			margin-bottom: 60px;

			@include bp(md) {
				margin-bottom: 0;
			}

			.text-14 {
				color: rgba($COLOR_TEXT, 0.7);

				strong {
					color: #262020;
					display: block;
					margin-bottom: 8px;
					font-weight: 500;
				}
			}

			.email-link {
				display: block;
				margin-top: 32px;
			}
		}

		.OpenPositionsCount {
			border-bottom: 1px solid rgba(#867f80, 0.2);
			text-transform: uppercase;
			padding-bottom: 30px;
			letter-spacing: 0.24em;
			margin-bottom: 10px;
			font-size: 9px;

			@include bp(md) {
				font-size: 12px;
				padding-bottom: 40px;
			}
		}
	}
}

.ListItem {
	width: 100%;
	display: flex;
	position: relative;
	flex-direction: column;
	padding: 16px 0;

	@include bp(md) {
		flex-direction: row;
		align-items: center;
		padding: 0;
	}

	&:after {
		content: '';
		background-image: linear-gradient(to right, #867f80 10%, rgba(255, 255, 255, 0) 0%);
		background-position: top;
		background-size: 8px 1px;
		background-repeat: repeat-x;
		width: 100%;
		height: 1px;
		display: block;
		position: absolute;
		bottom: 0;
		left: 0;
	}

	&:last-child {
		&:after {
			display: none;
		}
	}

	.ListItemName,
	.ListItemPosition {
		font-size: 14px;
		letter-spacing: 0.04em;
		color: $COLOR_TEXT;
	}

	.ListItemName {
		min-width: 270px;

		@include bp(md) {
			padding: 20px 0;
		}
	}

	.ListItemPosition {
		color: #494444;
		font-weight: $font-weight-roobert-light;
		margin-top: 4px;

		@include bp(md) {
			margin-top: 0;
		}
	}

	.ListItemLink {
		margin-left: auto;
		padding: 20px 36px 20px 36px;
		position: absolute;
		right: 0;
		top: 6px;

		@include bp(md) {
			position: relative;
			right: 0;
			top: 0px;
		}

		span {
			color: $COLOR_RED;
			font-size: 12px;
			letter-spacing: 0.24em;
			text-transform: uppercase;
			opacity: 0;
			transition: opacity 0.2s;
		}

		&:before,
		&:after {
			content: '';
			height: 6px;
			width: 6px;
			border: 1px solid $COLOR_RED;
			position: absolute;
			box-sizing: border-box;
			right: 8px;
			top: calc(50% - 3px);
			transform: rotateZ(45deg);
			transition: transform 0.3s;
		}

		&:before {
			right: 13px;
		}

		&:after {
			opacity: 0.4;
		}
	}

	&:hover {
		.ListItemLink {
			span {
				opacity: 1;
			}

			&:before {
				transform: translateX(calc(100% - 1px)) rotateZ(45deg);
			}

			&:after {
				transform: translateX(calc(-100% + 1px)) rotateZ(45deg);
			}
		}
	}
}
