	background-color: $COLOR_BEIGE;

	&.ErrorPage {
		.BurgerMenu-icon {
			display: none !important;
		}
	}
}

body {
	overflow-y: scroll;

	//overflow-x: hidden;
	color: $COLOR_TEXT;
	//min-height: 100vh;
}

.dg.ac {
	z-index: 50 !important;
}

* {
	box-sizing: border-box;
}

a,
a:visited,
a:active,
a:hover {
	//color: $COLOR_RED;
	text-decoration: none;
}

