#TemplateLayer {
	width: 100%;
	height: 100%;
}

.bgNoise {
	background-image: url('data:image/png;base64,');
}

.noNoise {
	background-image: none;

	.FooterModule {
		z-index: 4;
	}
}

.moduleInner {
	min-height: 100vh;
}

html:not(.HomeTemplatePage) {
	#TemplateLayer {
		img:not(.ignore) {
			top: 0;
			left: 0;
			position: absolute;
			opacity: 0;
			width: 100%;

			&.lazyloaded {
				transition: 0.5s opacity;
				opacity: 1;
			}
		}
	}
}

#SiteWrapper {
	width: 100%;
	height: 100%;
	//overflow: hidden;
	//position: relative;
}

.imageContainer {
	width: 100%;
	position: relative;

	iframe {
		top: 0;
		left: 0;
		position: absolute;
		width: 100%;
		height: 100%;
	}
}

.borderedImage {
	position: relative;

	&:before {
		height: calc(100% + 8px);
		width: calc(100% + 8px);
		content: '';
		position: absolute;
		border: 1px solid rgba(#494444, 0.2);
		top: -5px;
		left: -5px;
	}
}

.Separator {
	background-image: linear-gradient(to right, #867f80 10%, rgba(255, 255, 255, 0) 0%);
	background-position: top;
	background-size: 6px 1px;
	background-repeat: repeat-x;
	width: 100%;
	height: 1px;
	display: block;
}

.dotted-border {
	position: relative;

	&:after {
		content: '';
		background-image: linear-gradient(to right, #867f80 10%, rgba(255, 255, 255, 0) 0%);
		background-position: top;
		background-size: 6px 1px;
		background-repeat: repeat-x;
		width: 100%;
		height: 1px;
		display: block;
		position: absolute;
		bottom: 0;
		left: 0;
	}
}

.ButtonCta {
	height: 50px;
	padding-left: 18px;
	padding-right: 18px;
	background: transparent;
	font-size: 11px;
	font-family: $font-rogan;
	font-weight: $font-weight-rogan-bold;
	letter-spacing: 0.2em;
	text-transform: uppercase;
	position: relative;
	cursor: pointer;
	min-width: 120px;

	span {
		color: #231f20;
	}

	@include bp(sm) {
		height: 54px;
		font-size: 12px;
		padding-left: 24px;
		padding-right: 24px;
		min-width: 140px;
	}

	&.with-borders {
		border: 1px solid rgba(#dcd8d2, 0.4);
		box-sizing: border-box;
		transition: 0.2s;

		&:before {
			content: '';
			position: absolute;
			width: calc(100% - 6px);
			height: calc(100% - 6px);
			border: 1px solid #dcd8d2;
			transition: 0.2s;
			top: 3px;
			left: 3px;
			//transform: scale(0.96, 0.84);
			box-sizing: border-box;
		}
	}

	&.gradient {
		border: none;
		background: #ffffff;
		background: linear-gradient(90deg, rgba($COLOR_BEIGE, 0) 0%, rgba(255, 255, 255, 1) 36%);
	}

	&.arrowed {
		display: flex;
		align-items: center;

		&:hover {
			.arrow:after {
				transform: translateX(6px);
			}
		}

		.arrow {
			position: relative;
			height: 7px;
			width: 12px;
			display: inline-block;
			margin-left: 8px;

			&:before {
				content: '';
				width: 100%;
				height: 100%;
				background-image: url('../../assets/svg/embedded/chevron_right.svg');
				background-position: 0px top;
				background-size: 6px 7px;
				background-repeat: repeat-x;
				display: inline-block;
				opacity: 0.5;
				position: absolute;
				top: 0;
				left: 0;
			}

			&:after {
				content: '';
				width: 6px;
				height: 100%;
				background-image: url('../../assets/svg/embedded/chevron_right.svg');
				background-position: 0px top;
				background-size: 6px 7px;
				background-repeat: repeat-x;
				display: inline-block;
				position: absolute;
				top: 0;
				left: 0;
				transition: 0.2s transform;
			}
		}
	}
}

.underlined-link {
	position: relative;
	display: inline;
	text-decoration: none;
	color: #2f2425;
	transition: color 0.3s;

	@include bp(md) {
		color: #797672;
	}

	&:before {
		content: '';
		width: 100%;
		position: absolute;
		bottom: -2px;
		left: 0;
		height: 1px;
		background: #fa3a00;
	}

	&:hover {
		color: #2f2425;
	}
}

.closeLayerButton {
	position: absolute;
	top: 56px;
	right: 56px;
	height: 10px;
	width: 10px;
	z-index: 12;
	cursor: pointer;
	line-height: 0;

	@include bp(md) {
		top: 80px;
		right: 75px;
	}

	&:before,
	&:after {
		position: absolute;
		content: '';
		border: 2px solid $COLOR_RED;
		transition: transform 0.5s;
		border-radius: 50%;
	}

	&:before {
		height: 50px;
		width: 50px;
		border: 1px solid rgba($COLOR_RED, 0.3);
		left: -21px;
		top: -21px;
		transition: transform 0.2s;

		@include bp(md) {
			height: 60px;
			width: 60px;
			left: -26px;
			top: -26px;
		}
	}

	&:after {
		height: 62px;
		width: 62px;
		left: -28px;
		top: -28px;

		@include bp(md) {
			height: 72px;
			width: 72px;
			left: -33px;
			top: -33px;
		}
	}

	&:hover {
		&:before {
			transform: scale(1.2, 1.2);
		}

		svg {
			transform: scale(1.2, 1.2);
		}
	}

	svg {
		width: 100%;
		transition: transform 0.2s 0.05s;
	}
}

.arrow-hover-left {
	position: relative;

	&:before {
		position: absolute;
		content: '';
		top: calc(50% - 4px);
		right: 2px;
		height: 16px;
		width: 25px;
		background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg width='16px' height='9px' viewBox='0 0 16 9' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Ctitle%3EGroup 2%3C/title%3E%3Cg id='Symbols' stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3E%3Cg id='Group' transform='translate(9.292893, 4.535534) scale(-1, 1) translate(-9.292893, -4.535534) translate(-0.000000, 1.000000)'%3E%3Cg id='Group-2' transform='translate(3.585786, 0.000000)'%3E%3Cpolygon id='Rectangle-Copy-4' stroke='%23FA3B02' transform='translate(3.535534, 3.535534) rotate(-315.000000) translate(-3.535534, -3.535534) ' points='1.03553391 1.03553391 6.03553391 1.03553391 6.03553391 6.03553391 1.03553391 6.03553391'%3E%3C/polygon%3E%3Crect id='Rectangle' fill='%23FA3B02' transform='translate(11.000000, 3.500000) scale(-1, 1) translate(-11.000000, -3.500000) ' x='7' y='3' width='8' height='1'%3E%3C/rect%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
		background-repeat: no-repeat;
		background-size: contain;
		transition: transform 0.3s;
	}

	&:after {
		content: '';
		height: 6px;
		width: 6px;
		border: 1px solid #f53a02;
		position: absolute;
		box-sizing: border-box;
		right: 0px;
		top: calc(50%);
		transform: rotateZ(45deg);
		transition: transform 0.3s;
		opacity: 0.4;
	}

	@include bp(md) {
		padding-right: 36px;

		&:before {
			right: 2px;
		}

		&:after {
			right: 0px;
		}

		&:hover {
			&:before {
				transform: translateX(6px);
			}
		}
	}
}

.arrow-hover-back {
	@extend .arrow-hover-left;
	padding-left: 30px;

	&:before {
		top: calc(50% - 5px);
		left: 0px;
		transform: rotateZ(180deg);
		width: 20px;
		height: 11px;
	}

	&:after {
		width: 5px;
		height: 5px;
		left: -2px;
		top: calc(50% - 2px);
	}

	@include bp(md) {
		&:before {
			top: calc(50% - 5px);
			//left: -26px;
			transform: rotateZ(180deg);
			width: 20px;
			height: 11px;
		}

		&:after {
			width: 5px;
			height: 5px;
			//left: -28px;
			top: calc(50% - 2px);
		}

		&:hover {
			&:before {
				transform: translateX(-5px) rotateZ(180deg);
			}
		}
	}
}

