		position: absolute;
		content: '';
		top: calc(50% - 8px);
		left: 0px;
		height: 16px;
		width: 25px;
		background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg width='16px' height='9px' viewBox='0 0 16 9' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Ctitle%3EGroup 2%3C/title%3E%3Cg id='Symbols' stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3E%3Cg id='Group' transform='translate(9.292893, 4.535534) scale(-1, 1) translate(-9.292893, -4.535534) translate(-0.000000, 1.000000)'%3E%3Cg id='Group-2' transform='translate(3.585786, 0.000000)'%3E%3Cpolygon id='Rectangle-Copy-4' stroke='%23FA3B02' transform='translate(3.535534, 3.535534) rotate(-315.000000) translate(-3.535534, -3.535534) ' points='1.03553391 1.03553391 6.03553391 1.03553391 6.03553391 6.03553391 1.03553391 6.03553391'%3E%3C/polygon%3E%3Crect id='Rectangle' fill='%23FA3B02' transform='translate(11.000000, 3.500000) scale(-1, 1) translate(-11.000000, -3.500000) ' x='7' y='3' width='8' height='1'%3E%3C/rect%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
		background-repeat: no-repeat;
		background-size: contain;
		transition: transform 0.3s;
	}

	&:after {
		content: '';
		height: 6px;
		width: 6px;
		border: 1px solid #f53a02;
		position: absolute;
		box-sizing: border-box;
		left: 21px;
		top: calc(50% - 4px);
