.LandingIntro {
	@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
		margin-bottom: 64px;
	}

	h1 {
		font-family: $font-roobert;
		font-weight: $font-weight-roobert-light;
		color: rgb(16, 36, 52);
		font-size: 20px;
		line-height: 1.4;
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
			font-size: 24px;
			line-height: 1.33;
		}

		b {
			font-weight: $font-weight-roobert-medium;
		}

		&:nth-child(2) {
			position: absolute;
			top: 0;
		}
	}

	.blurCharacter {
		span {
			filter: blur(10px);
		}
	}

	.ChapterIntro-titleWrapper {
		position: relative;
	}
}

.LandingMenuIntroWrapper {
	position: absolute;
	bottom: 23px;
	left: 20px;
	left: calc(20px + env(safe-area-inset-left));
	right: 20px;
	right: calc(20px + env(safe-area-inset-right));
	@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
		bottom: $TABLET_MARGIN;
		left: calc(#{$TABLET_MARGIN} + env(safe-area-inset-left));
	}

	@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: $BREAKPOINT_HEIGHT) {
		bottom: calc(var(--leftMargin) - 10px);
		left: var(--leftMargin);
	}
}

.LandingMenu {
	display: none;
	flex-flow: row;

	@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
		display: flex;
	}

	ul,
	li {
		list-style: none;
		padding: 0;
		margin: 0;
	}

	ul:nth-child(2) {
		margin-left: 30px;
	}

	a {
		margin: 0;
		font-size: 11px;
		font-family: $font-rogan;
		color: rgb(0, 51, 91);
		font-weight: bold;
		text-transform: uppercase;
		line-height: 1.818;
		position: relative;
		padding-left: 18px;
		display: flex;
		flex-flow: row;
		align-items: center;

		&:before {
			content: '';
			position: absolute;
			width: 4px;
			height: 4px;
			background: #322fb3;
			display: block;
			left: 0px;
			margin-top: -2px;
			transform: rotateZ(45deg);
			transition: 0.2s;
		}

		transition: 0.2s;
		@media (hover: hover) {
			&:hover {
				//opacity: 0.5;
				color: black;
				transform: translateX(3px);

				&:before {
					transform: translateX(-3px) rotateZ(45deg);
				}
			}
		}
	}
}

.PlayNow {
	position: absolute;

	span {
		font-family: $font-rogan;
		font-weight: $font-weight-rogan-bold;
		margin-top: 3px;
	}

	font-size: 11px;
	color: #231f20;
	letter-spacing: 2px;
	display: flex;
	align-items: center;
	justify-content: center;
	text-transform: uppercase;
	top: 18px;
	right: 18px;
	right: calc(18px + env(safe-area-inset-right));
	height: 40px;
	padding-left: 17px;
	padding-right: 18px;
	background-image: linear-gradient(
		257deg,
		#ffffff 62%,
		rgba(255, 255, 255, 0.91) 68%,
		rgba(255, 255, 255, 0.79) 73%,
		rgba(255, 255, 255, 0.41) 83%,
		rgba(255, 255, 255, 0.18) 91%,
		rgba(255, 255, 255, 0) 100%,
		rgba(255, 255, 255, 0) 100%
	);
	@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
		top: $TABLET_MARGIN;
		right: calc(#{$TABLET_MARGIN} + env(safe-area-inset-right));
		height: 54px;
		font-size: 12px;
		span {
			margin-top: 2px;
		}
		padding-left: 25px;
		padding-right: 23px;
	}

	@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: $BREAKPOINT_HEIGHT) {
		top: 60px;
		right: calc(60px + env(safe-area-inset-right));
	}

	svg {
		width: 10px;
		height: 7px;
		margin-left: 6px;
	}
}

.LandingTrailer {
	position: absolute;
	border: 1px solid rgba(#261b7d, 0.3);
	line-height: 0;
	display: none;
	@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
		bottom: $TABLET_MARGIN;
		right: calc(#{$TABLET_MARGIN} + env(safe-area-inset-right));
	}
	@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: $BREAKPOINT_HEIGHT) {
		bottom: 54px;
		right: calc(54px + env(safe-area-inset-right));
	}
	@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
		display: block;
	}

	div {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;

		svg {
			width: 42px;
			height: 42px;
			margin-right: -3px;
			transition: 0.2s;
		}
	}

	span {
		margin-top: 1px;
		font-family: $font-rogan;
		font-weight: $font-weight-rogan-bold;
		font-size: 12px;
		color: #ffffff;
		letter-spacing: 0.24em;
		text-transform: uppercase;
		transition: 0.2s;
	}

	@media (hover: hover) {
		&:hover {
			svg {
				transform: translateX(-3px);
			}

			span {
				transform: translateX(3px);
			}
		}
	}

	img {
		padding: 7px;
		width: calc(272 / 1440 * 100vw);
		height: calc(169 / 1440 * 100vw);
	}
}

.MobileTrailerLink {
	display: flex;
	flex-flow: row;
	align-items: center;
	margin-top: 16px;
	@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
		display: none;
	}

	div {
		border-radius: 50%;
		width: 24px;
		height: 24px;
		border: 1px solid $COLOR_PURPLE;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-right: 8px;
	}

	svg {
		margin-left: 1px;
		width: 6px;
		height: 8px;
	}

	span {
		font-family: $font-rogan;
		font-weight: $font-weight-rogan-bold;
		font-size: 10px;
		color: $COLOR_PURPLE;
		letter-spacing: 1.8px;
		line-height: 12px;
		text-transform: uppercase;
	}
}

.LandingEnter-hitArea {
	position: absolute;
	width: 50vw;
	left: calc(50% - 25vw);
	height: 60vh;
	top: calc(50% - 30vh);
	background: rgba(#ff2080, 0.2);
	cursor: pointer;
	opacity: 0;
	visibility: hidden;
}

.LandingEnter-largeDiamond {
	position: absolute;
	top: calc(50% - 11px);
	left: calc(50% - 12px);
	width: 22px;
	height: 22px;
	display: block;
	transform: rotateZ(45deg);
	border: 1px solid rgba(#322fb3, 0.2);
	pointer-events: none;
	opacity: 0;
}

.LandingEnter {
	pointer-events: none;
	position: absolute;
	top: calc(50% - 7px);
	left: calc(50% - 80px);
	width: 160px;
	display: flex;
	flex-flow: row;
	justify-content: center;
	align-content: center;
	font-family: $font-rogan;
	font-weight: $font-weight-rogan-bold;
	font-size: 11px;
	color: $COLOR_PURPLE;
	letter-spacing: 3px;
	text-transform: uppercase;
	margin-left: 2px;
	opacity: 0;

	.LandingEnter-Diamond {
		margin: 0 14px;
		width: 6px;
		height: 6px;
		background: #322fb3;
		display: block;
		margin-top: 4px;
		transform: rotateZ(45deg);
	}

	user-select: none;

	:last-child {
		margin-left: 2px;
	}
}

.SoundMute {
	width: 14px;
	height: 14px;
	line-height: 0;
	cursor: pointer;
	padding: 13px;
	box-sizing: content-box;
	margin-right: -13px;

	path {
		transition: 0.2s;
		transform-origin: bottom;
	}

	/*	path:nth-child(1) {
		animation: barY 0.7s infinite ease-in-out;
		animation-delay: 0.1s;
	}

	path:nth-child(2) {
		animation: barY 0.5s infinite linear;
		animation-delay: 0.3s;
	}

	path:nth-child(3) {
		animation: barY 0.6s infinite ease-out;
		animation-delay: 0.2s;
	}

	path:nth-child(4) {
		animation: barY 0.8s infinite linear;
	}*/

	&.off {
		path {
			transform: scaleY(0.2);
			//animation: none;
			opacity: 0.5;
		}
	}
}

@keyframes barY {
	0% {
		transform: scaleY(0.2);
		opacity: 0.5;
	}
	100% {
		transform: scaleY(1);
		opacity: 1;
	}
}

.BottomBar-right {
	position: absolute;
	bottom: 23px;
	right: 20px;
	right: calc(20px + env(safe-area-inset-right));
	z-index: 2;
	/*    @media (min-width: 360px) and (min-height: $BREAKPOINT_HEIGHT) {
            bottom: 38px;
            right: 35px;
            right: calc(35px + env(safe-area-inset-right));
        }*/
	@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
		bottom: 38px;
		right: $TABLET_MARGIN;
		right: calc(#{$TABLET_MARGIN} + env(safe-area-inset-right));
	}
	@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: $BREAKPOINT_HEIGHT) {
		bottom: calc(var(--leftMargin) - 10px);
		right: var(--leftMargin);
	}
	display: flex;
	flex-flow: row;
	align-items: center;
	opacity: 0;
	visibility: hidden;
}

.SmallTrailerLink {
	display: flex;
	flex-flow: row;
	align-items: center;
	margin-left: 15px;

	div {
		border-radius: 50%;
		width: 16px;
		height: 16px;
		border: 1px solid rgba(#979797, 0.6);
		display: flex;
		align-items: center;
		justify-content: center;
		margin-left: 8px;
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
			width: 24px;
			height: 24px;
		}
	}

	svg {
		margin-left: 1px;
		width: 5px;
		height: 6px;
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
			width: 6px;
			height: 8px;
		}
	}

	span {
		display: none;
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
			display: block;
		}
		font-family: $font-rogan;
		font-weight: $font-weight-rogan-bold;
		font-size: 11px;
		color: #2f2425;
		letter-spacing: 0.2em;
		line-height: 11px;
		margin-top: 1px;
		text-transform: uppercase;
	}
}

.ChapterPOIs {
	position: absolute;
	bottom: 19px;
	left: 20px;
	left: calc(20px + env(safe-area-inset-left));
	width: 240px;
	visibility: hidden;
	@media (min-width: 400px) and (min-height: 600px) {
		bottom: 23px;
	}
	@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
		bottom: $TABLET_MARGIN;
		left: $TABLET_MARGIN;
		left: calc(#{$TABLET_MARGIN} + env(safe-area-inset-left) + 17vw);
	}
	@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: $BREAKPOINT_HEIGHT) {
		bottom: calc(var(--leftMargin) - 10px);
		left: calc(var(--leftMargin) + 17vw);
	}
	@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
		height: 300px;
		width: 40vw;
	}
	margin-left: 25px;
	@media (min-width: 375px) {
		margin-left: 35px;
	}
	@media (min-width: 400px) {
		margin-left: 10vw;
	}

	div {
		position: absolute;
		left: 0;
		display: flex;
		align-items: flex-start;
		flex-flow: column;
		bottom: 0;
		//will-change: transform;
		@media (min-width: 400px) and (min-height: 600px) {
			padding-bottom: 20px;
		}
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
			padding-bottom: 40px;
		}

		h2 {
			font-family: $font-roobert;
			font-weight: $font-weight-roobert-light;
			color: #231f20;
			@include poly-fluid-sizing(
				'font-size',
				(
					320px: 20px,
					375px: 24px
				)
			);
			@include poly-fluid-sizing(
				'line-height',
				(
					320px: 27px,
					375px: 27px
				)
			);
			@media (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
				@include poly-fluid-sizing(
					'font-size',
					(
						320px: 20px,
						375px: 24px,
						1680px: 31px
					)
				);
				@include poly-fluid-sizing(
					'line-height',
					(
						320px: 27px,
						375px: 32px,
						1680px: 38px
					)
				);
			}
		}

		p {
			font-family: $font-roobert;
			color: #78746d;
			font-weight: normal;
			letter-spacing: 0.03em;
			@include poly-fluid-sizing(
				'font-size',
				(
					320px: 11px,
					375px: 13px
				)
			);
			@include poly-fluid-sizing(
				'line-height',
				(
					320px: 17px,
					375px: 20px
				)
			);
			@media (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
				@include poly-fluid-sizing(
					'font-size',
					(
						320px: 11px,
						375px: 13px,
						1680px: 16px
					)
				);
				@include poly-fluid-sizing(
					'line-height',
					(
						320px: 17px,
						375px: 20px,
						1680px: 24px
					)
				);
			}
			@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
				margin-top: 12px;
			}
		}
	}

	@media (min-width: 400px) and (min-height: 600px) {
		&:before {
			content: '';
			background-color: rgb(58, 48, 43);
			opacity: 0.302;
			position: absolute;
			width: 40px;
			height: 2px;
			bottom: 0;
			left: 0;
		}
	}
}

#webglmessage {
	position: relative;
	z-index: 10;
	width: 100% !important;
	max-width: 400px;

	a {
		color: $COLOR_ORANGE !important;
		text-decoration: underline;
	}

	&:after {
		display: block;
		margin-top: 10px;
		content: 'This experience will not work without WebGL, please visit some of the other pages accessible from the menu.';
	}
}

.ScrollTimer {
	display: none;
	@supports (contain: content) {
		opacity: 0;
		position: fixed;
		left: 18px;
		left: calc(18px + env(safe-area-inset-left));
		bottom: 23px;
		user-select: none;
		pointer-events: none;
		@media (min-height: 480px) {
			display: flex;
		}
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
			bottom: $TABLET_MARGIN;
			left: $TABLET_MARGIN - 1px;
			left: calc(#{$TABLET_MARGIN} - 1px + env(safe-area-inset-left));
		}

		@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: $BREAKPOINT_HEIGHT) {
			bottom: calc(var(--leftMargin) - 10px);
			left: var(--leftMargin);
		}
		flex-flow: row;
		svg {
			width: 9px;
			height: 16px;
			@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
				margin-top: 5px;
				width: 13px;
				height: 23px;
			}
		}

		div {
			display: none;
			@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
				display: flex;
			}
			flex-flow: column;
			align-items: flex-start;
			margin-left: 28px;

			span {
				font-size: 11px;
				font-family: $font-rogan;
				color: rgba(58, 48, 43, 0.6);
				font-weight: $font-weight-rogan-bold;
				text-transform: uppercase;
			}

			.ScrollTimer-time {
				contain: content;
				width: 100px;
				font-size: 13px;
				font-feature-settings: 'tnum';
				font-variant-numeric: tabular-nums;
				font-family: $font-rogan;
				color: rgb(58, 48, 43);
			}
		}
	}
}
