.container {
	width: 100%;
	padding-left: 10px;
	padding-right: 10px;
	max-width: $MAX_WIDTH;
	margin: 0 auto;

	@media screen and (min-width: $BREAKPOINT_MOBILE_XSMALL) {
		padding-left: 34px;
		padding-right: 34px;
	}
	@media screen and (min-width: $BREAKPOINT_MOBILE_BIG) {
		padding-left: $GLOBAL_PADDING;
		padding-right: $GLOBAL_PADDING;
	}

	.innerContainer {
		position: relative;
		width: 100%;
	}

	.grid {
		//@extend .innerContainer;

		position: relative;
		display: flex;
		flex-wrap: wrap;
		margin: 0 (-16px / 2);

		@include bp(md) {
			margin: 0 (-$GRID_GUTTER_WIDTH / 2);
		}
	}

	.grid,
	.innerContainer {
	}

	@for $i from 1 to 15 {
		.col-#{$i} {
			width: calc((100% / 14) * #{$i});
			padding: 0 (16px / 2);

			@include bp(md) {
				padding: 0 ($GRID_GUTTER_WIDTH / 2);
			}
		}
	}

	@for $i from 1 to 15 {
		.col-sm-#{$i} {
			@media screen and (min-width: $BREAKPOINT_MOBILE_BIG) {
				width: calc((100% / 14) * #{$i});
				padding: 0 (16px / 2);

				@include bp(md) {
					padding: 0 ($GRID_GUTTER_WIDTH / 2);
				}
			}
		}
	}

	@for $i from 1 to 15 {
		.col-md-#{$i} {
			@media screen and (min-width: $BREAKPOINT_TABLET_LANDSCAPE) {
				width: calc((100% / 14) * #{$i});
				padding: 0 (16px / 2);

				@include bp(md) {
					padding: 0 ($GRID_GUTTER_WIDTH / 2);
				}
			}
		}
	}

	@for $i from 0 to 14 {
		.offset-#{$i} {
			margin-left: calc((100% / 14) * #{$i});
		}
	}

	@for $i from 0 to 14 {
		.offset-sm-#{$i} {
			@media screen and (min-width: $BREAKPOINT_MOBILE_BIG) {
				margin-left: calc((100% / 14) * #{$i});
			}
		}
	}

	@for $i from 0 to 14 {
		.offset-md-#{$i} {
			@media screen and (min-width: $BREAKPOINT_TABLET_LANDSCAPE) {
				margin-left: calc((100% / 14) * #{$i});
			}
		}
	}
}
