@import 'mixins/poly_fluid_sizing';
@import 'mixins/ui';

.disableSelection {
	user-select: none;
	-webkit-user-drag: none;
	-webkit-touch-callout: none;
}

$media: screen;
$feature: min-width;

@mixin bp($breakpoint) {
	@if $breakpoint == xxs {
		@media #{$media} and ($feature: $BREAKPOINT_MOBILE_XSMALL) {
			@content;
		}
	}
	@if $breakpoint == xs {
		@media #{$media} and ($feature: $BREAKPOINT_MOBILE_SMALL) {
			@content;
		}
	} @else if $breakpoint == sm {
		@media #{$media} and ($feature: $BREAKPOINT_MOBILE_BIG) {
			@content;
		}
	} @else if $breakpoint == md {
		@media #{$media} and ($feature: $BREAKPOINT_TABLET_LANDSCAPE) {
			@content;
		}
	} @else if $breakpoint == mds {
		@media #{$media} and ($feature: $BREAKPOINT_LANDING_TABLET_LANDSCAPE) {
			@content;
		}
	} @else if $breakpoint == tp {
		@media #{$media} and ($feature: $BREAKPOINT_TABLET_PORTRAIT) {
			@content;
		}
	} @else if $breakpoint == lg {
		@media #{$media} and ($feature: $BREAKPOINT_DESKTOP_BIG) {
			@content;
		}
	} @else if $breakpoint == dp {
		@media #{$media} and ($feature: $BREAKPOINT_DESKTOP_MARGIN) {
			@content;
		}
	} @else if $breakpoint == xl {
		@media #{$media} and ($feature: $BREAKPOINT_DESKTOP_MAX) {
			@content;
		}
	} @else if $breakpoint == menuCircles {
		@media #{$media} and ($feature: $BREAKPOINT_LANDING_TABLET_LANDSCAPE) and (min-height: $BREAKPOINT_HEIGHT_MENU) and (max-aspect-ratio: 2520/1043) and (min-aspect-ratio: 1024/768) {
			@content;
		}
	}
}

@function strip-unit($number) {
	@if type-of($number) == 'number' and not unitless($number) {
		@return $number / ($number * 0 + 1);
	}

	@return $number;
}

@function em($value, $base: 16) {
	@return strip-unit($value) / strip-unit($base) * 1em;
}
