.ExploreButton {
	position: absolute;
	top: -40px;
	left: -40px;
	width: 80px;
	height: 80px;
	cursor: pointer;
	pointer-events: none;
	visibility: hidden;
	@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
		top: -50px;
		left: -50px;
		width: 100px;
		height: 100px;
	}

	.ExploreButton-outer {
		position: absolute;
		top: calc(50% - 40px);
		left: calc(50% - 40px);
		border: 2px solid #fa3b02;
		border-radius: 50%;
		width: 80px;
		height: 80px;
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
			top: calc(50% - 50px);
			left: calc(50% - 50px);
			width: 100px;
			height: 100px;
		}
	}

	.ExploreButton-inner {
		position: absolute;
		opacity: 0.2;
		border: 1px solid #fa3b02;
		border-radius: 50%;

		width: 66px;
		height: 66px;
		top: calc(50% - 33px);
		left: calc(50% - 33px);
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
			width: 86px;
			height: 86px;
			top: calc(50% - 43px);
			left: calc(50% - 43px);
		}
	}

	.ExploreButton-line {
		width: 1px;
		background: #fa3b02;
		position: absolute;
		top: 50%;
		left: 50%;
		transform-origin: 0 0;
		transform: rotateZ(210deg) translateY(27px);
		height: 40px;
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
			transform: rotateZ(210deg) translateY(37px);
			height: 50px;
		}
	}

	.ExploreButton-lineDot {
		position: absolute;
		border: 1px solid #fa3b02;
		border-radius: 50%;
		width: 6px;
		height: 6px;
		top: 50%;
		left: 50%;
		transform-origin: 0 0;
		transform: rotateZ(210deg) translateY(27+39px) translateX(-2.5px);
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
			transform: rotateZ(210deg) translateY(37+49px) translateX(-2.5px);
		}
	}

	.ExploreButton-label {
		font-size: 10px;
		font-family: $font-rogan;
		color: rgba(39, 33, 33, 0.8);
		font-weight: $font-weight-rogan-bold;
		text-transform: uppercase;
		text-align: left;
		position: absolute;
		top: -32%;
		left: 105%;
		//transform-origin: 0 0;
		transform: translate3d(0, 0, 0);
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
			top: -34%;
			font-size: 11px;
		}
	}
}
