.PartnersModule {
	//background: url('../../assets/images/mobile-partner-gradient.png');
	//background-repeat: no-repeat;
	//background-position: bottom left;
	//background-size: contain;
	//
	//@include bp(sm) {
	//	background: url('../../assets/images/partner-background.png');
	//	background-repeat: no-repeat;
	//	background-position: center 0;
	//	background-size: 100%;
	//}
	position: relative;

	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		//transform: translateX(-50%);
		//min-width: 1680px;
		max-height: 1416px;
		height: 100%;
		width: 100%;
		//padding-bottom: 29.7619047619%;
		//height: 500px;
		background: url('/assets/svg/partners_header_mobile.svg') no-repeat;
		background-position: top center;
		background-size: 160% 103%;
		z-index: 4;
		box-sizing: border-box;

		@include bp(md) {
			background: url('/assets/svg/partners_header.svg') no-repeat;
			background-position: bottom center;
			background-size: cover;
			max-height: 1350px;
		}
	}

	.moduleInner {
		padding-top: 80px;
		padding-bottom: 80px;
		position: relative;
		z-index: 4;
		overflow: hidden;

		@include bp(sm) {
			padding-bottom: 150px;
		}
	}

	.PageHeader {
		margin-bottom: 216px;
		padding-top: 80px;
		position: relative;

		@include bp(md) {
			margin-bottom: 150px;
			height: 1100px;
			padding-top: 150px;
		}

		@include bp(md) {
			margin-bottom: 200px;
		}

		.eyebrow {
			margin-bottom: 12px;
		}

		.AboutPartnersText {
			margin-top: 42px;
			max-width: 320px;
		}

		.MainPartnersWrapper {
			position: relative;
			top: 0;
			left: 0;
			width: 100%;
			max-width: 550px;
			margin: auto;

			@include bp(md) {
				position: absolute;
				width: auto;
				max-width: 100%;
				margin: 0;
			}

			.Partner {
				width: 7vw;
				height: 7vw;
				border-radius: 50%;
				background: $COLOR_BEIGE;
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				min-width: 144px;
				min-height: 144px;
				max-width: 245px;
				max-height: 245px;
				margin-top: 85px;

				@include bp(xxs) {
					min-width: 168px;
					min-height: 168px;
				}

				@include bp(md) {
					position: absolute;
					min-width: 210px;
					min-height: 210px;
					margin-top: 0;
				}

				& > * {
					pointer-events: none;
				}

				img {
					max-width: 90px;
					position: relative;
					opacity: 1;

					@include bp(md) {
						max-width: 130px;
					}
				}

				&:after,
				&:before {
					content: '';
					border-radius: 50%;
					position: absolute;
					height: 100%;
					width: 100%;
					top: 0;
					left: 0;
					border: 1px solid $COLOR_BLUE;
					box-sizing: border-box;
					transition: transform 0.2s;
				}

				&:before {
					height: calc(100% + 12px);
					width: calc(100% + 12px);
					top: -6px;
					left: -6px;

					@include bp(md) {
						height: calc(100% + 24px);
						width: calc(100% + 24px);
						top: -12px;
						left: -12px;
					}
				}

				&:after {
					height: calc(100% + 24px);
					width: calc(100% + 24px);
					top: -12px;
					left: -12px;
					border-color: rgba($COLOR_BLUE, 0.2);

					@include bp(md) {
						height: calc(100% + 40px);
						width: calc(100% + 40px);
						top: -20px;
						left: -20px;
					}
				}

				.dash {
					position: absolute;
					width: 60px;
					height: 1px;
					background: $COLOR_BLUE;
					top: 7px;
					left: -70px;
					transform-origin: 100% 100%;

					&:before {
						content: '';
						height: 3px;
						width: 3px;
						position: absolute;
						right: -2px;
						top: -2px;
						border-radius: 50%;
						border: 1px solid $COLOR_BLUE;
					}
				}

				@include bp(md) {
					&:hover {
						&:before {
							transform: scale(1.072);
						}

						&:after {
							transform: scale(0.93);
						}
					}
				}

				&:nth-child(1) {
					top: 0px;
					left: -18px;
					@include bp(xxs) {
						left: -55px;
					}
					@include bp(md) {
						top: 150px;
						left: 0px;
						@include poly-fluid-sizing(
							'left',
							(
								$BREAKPOINT_TABLET_LANDSCAPE: 450px,
								$BREAKPOINT_DESKTOP_BIG: 700px
							)
						);
					}

					.content {
						right: 0;
						top: 100px;
						transform: translateX(calc(100% + 40px));
						@include bp(xxs) {
							transform: translateX(calc(100% + 50px));
						}
						@include bp(md) {
							top: 0;
							transform: translateX(calc(100% + 84px));
							@include poly-fluid-sizing(
								'right',
								(
									$BREAKPOINT_TABLET_LANDSCAPE: 50px,
									$BREAKPOINT_DESKTOP_BIG: 0
								)
							);
						}
					}

					.dash {
						width: 70px;
						left: -80px;
						transform: rotateZ(15deg);

						@include bp(md) {
							width: 110px;
							left: -120px;
							transform: rotateZ(-25deg);
						}
					}
				}

				&:nth-child(2) {
					top: 0px;
					margin-left: auto;
					margin-top: 180px;
					right: -18px;
					@include bp(xxs) {
						right: -55px;
					}
					@include bp(md) {
						top: 400px;
						left: 500px;
						right: auto;
						@include poly-fluid-sizing(
							'top',
							(
								$BREAKPOINT_TABLET_LANDSCAPE: 240px,
								$BREAKPOINT_DESKTOP_BIG: 310px
							)
						);
						@include poly-fluid-sizing(
							'left',
							(
								$BREAKPOINT_TABLET_LANDSCAPE: 490px,
								$BREAKPOINT_DESKTOP_BIG: 1000px
							)
						);
					}

					.content {
						left: 0;
						top: 0;
						text-align: right;
						transform: translateX(calc(-100% - 40px));
						@include bp(xxs) {
							transform: translateX(calc(-100% - 50px));
						}
						@include bp(md) {
							right: 0;
							top: 20%;
							left: auto;
							@include poly-fluid-sizing(
								'right',
								(
									$BREAKPOINT_TABLET_LANDSCAPE: 30px,
									$BREAKPOINT_DESKTOP_BIG: 0
								)
							);
							transform: translateX(calc(100% + 64px));
							text-align: left;
						}
					}

					.dash {
						transform: rotateZ(30deg);
						width: 70px;
						right: -80px;
						//top: 0px;
						left: auto;
						transform-origin: 0% 0%;

						&:before {
							left: 0;

							@include bp(md) {
								right: 0;
								left: auto;
							}
						}

						@include bp(md) {
							transform: rotateZ(-10deg);
							width: 90px;
							left: -100px;
							right: auto;
							transform-origin: 100% 100%;
						}
					}
				}

				&:nth-child(3) {
					top: 0px;
					left: -18px;
					@include bp(xxs) {
						left: -55px;
					}
					@include bp(md) {
						top: 580px;
						@include poly-fluid-sizing(
							'left',
							(
								$BREAKPOINT_TABLET_LANDSCAPE: 270px,
								$BREAKPOINT_DESKTOP_BIG: 600px
							)
						);
					}

					.content {
						right: 0;
						top: 10px;
						transform: translateX(calc(100% + 40px));
						@include bp(xxs) {
							transform: translateX(calc(100% + 50px));
						}
						@include bp(md) {
							right: auto;
							top: 30%;
							@include poly-fluid-sizing(
								'left',
								(
									$BREAKPOINT_TABLET_LANDSCAPE: 30px,
									$BREAKPOINT_DESKTOP_BIG: 0
								)
							);
							transform: translateX(calc(-100% - 64px));
							text-align: right;
						}
					}

					.dash {
						width: 70px;
						left: -80px;
						transform: rotateZ(-10deg);

						@include bp(md) {
							width: 70px;
							right: -80px;
							left: auto;
							transform-origin: 0% 0%;
							transform: rotateZ(10deg);
						}

						&:before {
							@include bp(md) {
								left: 0;
							}
						}
					}
				}

				.content {
					position: absolute;
					width: 146px;
					@include bp(xxs) {
						width: 166px;
					}
					@include bp(md) {
						width: 15vw;
						max-width: 270px;
						min-width: 245px;
					}

					p:nth-of-type(1) {
						@extend .uppercase;
						font-size: 12px;
						color: $COLOR_WHITE;
						margin-bottom: 15px;
					}

					p:nth-of-type(2) {
						color: rgba($COLOR_WHITE, 0.7);
					}
				}
			}
		}
	}

	.StackHolders {
		//justify-content: center;
	}

	.StackHoldersSection {
		.SectionTitle {
			margin-bottom: 50px;

			@include bp(md) {
				margin-bottom: 100px;
			}
		}
	}

	.StackHoldersList {
		.StackHolder {
			position: relative;
			padding-bottom: 87.5%;
			overflow: hidden;

			&:before {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				height: 1px;
				width: 100%;
				background: $COLOR_RED;
				opacity: 0;
				transition: opacity 0.3s;
			}

			&:hover {
				&:before {
					opacity: 1;
				}
			}
		}

		.StackHolderInner {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			border-top: 1px solid rgba($COLOR_GREY, 0.2);

			.StackHolderName {
				position: absolute;
				top: 14px;
				left: 0;
				@extend .uppercase;
				color: $COLOR_GREY;
				font-size: 11px;
				display: none;

				@include bp(md) {
					display: block;
				}
			}

			img {
				position: relative;
				max-width: 100px;
				max-height: 42px;

				@include bp(md) {
					max-width: 140px;
					max-height: 60px;
				}
			}
		}
	}
}
