.video-js .vjs-time-control {
	@include flex(none);
	font-size: 14px;
	line-height: 14px;
	width: auto;
	font-family: 'Source Sans Pro', serif;
	font-weight: bold;
	display: flex;
	flex-flow: column;
	justify-content: flex-end;
	bottom: -6px;
	margin-bottom: -6px;
}

.video-js .vjs-remaining-time {
	display: none;
}
.video-js .vjs-current-time {
	margin-left: 18px;
}
.video-js .vjs-time-divider {
	padding-left: 3px;
	padding-right: 3px;
}
