.Newsletter {
	&.error {
		.Newsletter-email {
		}

		.Newsletter-signup {
			filter: grayscale(1);
			opacity: 0.6;
		}
	}

	&.disabled {
		.Newsletter-email {
			opacity: 0.5;
			pointer-events: none;
		}

		.Newsletter-signup {
			display: none;
		}

		.Newsletter-loader {
			display: block;
		}
	}

	.errors {
		color: $COLOR_RED;
		position: absolute;
		bottom: -4px;
		width: 100%;
		font-size: 11px;

		span {
			opacity: 0;
			text-align: center;
			transition: opacity 0.4s;
			display: block;
			position: absolute;
			top: 0;
			width: 100%;

			@include bp(tp) {
				text-align: right;
			}

			&.active {
				opacity: 1;
			}
		}
	}

	label {
		font-family: $font-rogan;
		font-weight: $font-weight-rogan-bold;
		text-transform: uppercase;
		color: #dce1ec;
		letter-spacing: 0.24em;
		font-size: 9px;
		line-height: 12px;
		margin-bottom: 16px;
		@include bp(tp) {
			max-width: 160px;
			text-align: right;
			font-size: 11px;
			line-height: 16px;
		}
		@include bp(mds) {
			margin-bottom: 19px;
		}
	}

	input[type='email'] {
		height: 48px;
		width: calc(100% - 10px);
		background: transparent;
		padding: 5px 15px;
		border: 1px solid rgba(#90a3cf, 0.3);
		outline-offset: 4px;
		outline: 1px solid rgba(#90a3cf, 0.2);
		font-family: $font-roobert;
		font-weight: $font-weight-roobert-regular;
		color: #dce1ec;
		font-size: 14px;
		letter-spacing: 0.4px;
		line-height: 16px;
		margin: 5px;
		border-radius: 0;
		-webkit-appearance: none;
		@include bp(mds) {
			height: 58px;
			font-size: 16px;
		}

		&::placeholder {
			font-family: $font-roobert;
			font-weight: $font-weight-roobert-regular;
			font-size: 12px;
			color: #dce1ec;
			letter-spacing: 0.4px;
			line-height: 14px;

			@include bp(mds) {
				font-size: 14px;
			}
		}
		&::-moz-placeholder {
			line-height: 44px;
		}
	}
}

.Newsletter-title {
	text-align: center;
	position: relative;

	@include bp(tp) {
		text-align: right;
	}

	.success {
		display: none;
		position: absolute;
		bottom: 0;
		width: 100%;
		left: 0;

		br {
			@include bp(mds) {
				display: none;
			}
		}
	}
}

.NewsletterInputWrapper {
	position: relative;
	width: 100%;
	margin-bottom: 32px;
	max-width: 304px;
	@include bp(mds) {
		max-width: 360px;
	}

	.Newsletter-signup {
		position: absolute;
		right: 0;
		top: 0;
		height: 100%;
		@extend .arrow-hover-left;
		width: 60px;
		padding: 0;
		cursor: pointer;

		.MainMenu-newsletter-signup-border {
			position: absolute;
			top: 13px;
			bottom: 13px;
			left: 0px;
			width: 1px;
			opacity: 0.5;
			background-image: url("data:image/svg+xml,%3Csvg width='2' height='2' viewBox='0 0 2 5' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect width='1' height='1' fill='%2390A3CF'/%3E%3C/svg%3E%0A");
			background-size: 2px 5px;
			background-repeat: repeat-y;
		}

		input {
			cursor: pointer;
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			opacity: 0;
			-webkit-appearance: none;
		}

		&:before {
			top: calc(50% - 5px);
			right: calc(50% - 5px);
			width: 20px;
			height: 11px;
			pointer-events: none;
		}

		&:after {
			width: 5px;
			height: 5px;
			right: calc(50% - 7px);
			top: calc(50% - 2px);
			pointer-events: none;
		}

		@include bp(md) {
			&:before {
				top: calc(50% - 5px);
				width: 20px;
				height: 11px;
			}

			&:after {
				width: 5px;
				height: 5px;
				top: calc(50% - 2px);
			}

			@media (hover: hover) {
				&:hover {
					&:before {
						transform: translateX(5.25px);
					}
				}
			}
		}
	}
}

.Newsletter-loader {
	width: 25px;
	height: 25px;
	border: 1px solid $COLOR_RED;
	border-right-color: transparent;
	border-radius: 50%;
	position: absolute;
	top: 22px;
	right: 20px;
	-webkit-animation: loader3 1.4s infinite linear;
	animation: loader3 1.4s infinite linear;
	-webkit-transform: translateZ(0);
	-ms-transform: translateZ(0);
	transform: translateZ(0);
	display: none;
}

@keyframes loader3 {
	0% {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}
