.ChapterIntro-overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	/*  display: flex;
      flex-flow: column;
      justify-content: center;
      align-items: center;*/
	user-select: none;
	pointer-events: none;

	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: 1fr;
	grid-column-gap: 0px;
	grid-row-gap: 0px;
	//contain: content;
}

.ChapterIntro {
	grid-area: 1 / 1 / 1 / 1;
	text-align: center;
	display: flex;
	flex-flow: column;
	justify-content: center;
	align-items: center;
	user-select: none;
	pointer-events: none;
	//contain: content;
	//transform: translateX(-50%) translateY(-50%);
	h1 {
		font-family: $font-roobert;
		font-weight: $font-weight-roobert-light;
		@include poly-fluid-sizing(
			'font-size',
			(
				320px: 25px,
				375px: 30px,
				1680px: 59px
			)
		);
		@include poly-fluid-sizing(
			'line-height',
			(
				320px: 34px,
				375px: 40px,
				1680px: 72px
			)
		);
		color: rgb(22, 23, 27);
		margin-top: 11px;
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
			letter-spacing: -0.02em;
			margin-top: 13px;
		}

		&:nth-child(2) {
			position: absolute;
			top: 0;
		}
		/*        div {
            will-change: transform;
        }*/
	}

	span {
		will-change: transform;
	}

	.blurCharacter {
		span {
			filter: blur(10px);
		}
	}

	.ChapterIntro-titleWrapper {
		position: relative;
	}

	span.ChapterIntro-lead {
		font-size: 15px;
		line-height: 1.667;
		will-change: transform;

		@include poly-fluid-sizing(
			'font-size',
			(
				320px: 9px,
				375px: 9px,
				1680px: 15px
			)
		);
		@include poly-fluid-sizing(
			'line-height',
			(
				320px: 12px,
				375px: 12px,
				1680px: 20px
			)
		);

		font-family: $font-rogan;
		color: $COLOR_ORANGE;
		font-weight: $font-weight-rogan-bold;
		text-transform: uppercase;
		text-align: center;
		letter-spacing: 0.2em;
	}

	span.ChapterIntro-number {
		will-change: transform;
		border-radius: 50%;
		margin-top: 27px;
		width: 44px;
		height: 44px;
		font-size: 12px;
		padding-left: 0px;
		font-feature-settings: 'tnum';
		font-variant-numeric: tabular-nums;
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
			margin-top: 50px;
			width: 50px;
			height: 50px;
			font-size: 18px;
			padding-left: 0px;
		}
		display: flex;
		align-items: center;
		justify-content: center;
		font-family: $font-rogan;
		border: 1px solid #fa3a00;
		color: rgb(22, 23, 27);
		font-weight: $font-weight-rogan-bold;
		line-height: 0.778;
		position: relative;

		&:before {
			content: '';

			border-radius: 50%;
			width: 52px;
			height: 52px;
			left: -6px;
			@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
				width: 64px;
				height: 64px;
				left: -9px;
			}
			position: absolute;
			display: block;
			align-items: center;
			justify-content: center;
			border: 1px solid #fa3a00;
			opacity: 0.2;
		}
	}
}
