.NewsArticleTemplate {
	min-height: 100vh;
	padding-top: 90px;

	@include bp(sm) {
		padding-top: 150px;
	}

	.BackToTop {
		position: fixed;
		bottom: 50px;
		right: 50px;
		z-index: 1;
		color: #78746d;
		cursor: pointer;

		svg {
			transform: rotate(180deg);
			top: -2px;
			position: relative;
			right: -3px;
		}
	}

	.ImagePopIn {
		position: fixed;
		top: 0;
		left: 0;
		height: 100vh;
		width: 100%;
		z-index: 11;
		display: flex;
		align-items: center;
		justify-content: center;
		opacity: 0;
		visibility: hidden;

		.Layer {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: #201f1d;
			z-index: 2;
		}

		.PopInContent {
			position: relative;
			z-index: 5;
		}

		.closeLayerButton {
			z-index: 5;
		}

		@include bp(sm) {
			padding: 0 32px;
		}

		img {
			opacity: 1 !important;
		}

		p {
			color: #fff;
			text-align: center;
			margin-top: 6vh;
			padding: 0 12px;
			font-size: 11px;
			line-height: em(16px, 11px);
		}
	}

	.ArticleTopBar {
		margin-bottom: 64px;

		@include bp(sm) {
			margin-bottom: 80px;
		}

		@include bp(md) {
			margin-bottom: 30px;
		}

		.grid {
			align-items: center;
		}

		.desktop {
			display: none;

			@include bp(md) {
				display: block;
			}
		}

		.mobile {
			display: block;

			@include bp(md) {
				display: none;
			}
		}

		.Line {
			background: rgba(#2f2425, 0.2);
			display: block;
			margin-bottom: 42px;
			width: 100%;
			height: 1px;
		}

		.ArticleLinkBack {
			color: #2f2425;
			letter-spacing: 0.06em;
			position: relative;
			font-size: 9px;
			display: flex;
			align-items: center;

			@include bp(sm) {
				font-size: 12px;
			}

			svg {
				width: 20px;
				margin-right: 8px;
			}
		}

		.TopBarRightPart {
			display: flex;
			align-items: center;
			justify-content: flex-end;
		}

		.ArticleDate {
			color: rgba(#2f2425, 0.7);
			font-size: 9px;

			@include bp(sm) {
				font-size: 12px;
			}

			&.mobile {
				margin-right: 6px;

				&:after {
					content: "";
					margin-left: 6px;
				}
			}
		}

		.ArticleTag {
			text-align: right;
			color: #867f80;
			font-size: 9px;
			letter-spacing: 0.06em;


			@include bp(sm) {
				font-size: 12px;
			}

			a {
				color: #161414;
				position: relative;

				&:before {
					content: '';
					width: 100%;
					position: absolute;
					bottom: -2px;
					left: 0;
					height: 1px;
					background: #fa3a00;
					opacity: 0;
					transition: .2s opacity;
				}

				&:hover:before {
					opacity: 1;
				}
			}
		}
	}

	.HeroImage {
		margin-bottom: 70px;
	}

	.ArticleBlocks {
		padding-bottom: 80px;

		@include bp(sm) {
			padding-bottom: 200px;
		}
		//min-height: 100vh;
	}

	.ArticleIntroParagraph {
		margin-bottom: 60px;

		.ArticleTitle {
			font-size: 30px;
			line-height: 1.125em;
			color: #2f2425;
			margin-bottom: 16px;
			font-weight: $font-weight-roobert-light;

			@include poly-fluid-sizing("font-size", ($BREAKPOINT_MOBILE_SMALL: 24px, $BREAKPOINT_DESKTOP_BIG: 48px));

			@include bp(sm) {
				margin-bottom: 32px;
			}

			@include bp(md) {
				margin-bottom: 2.7em;
			}
		}

		p {
			font-size: 16px;
			letter-spacing: 0.02em;
			line-height: 1.4em;
			color: rgba(#2f2425, 0.7);
			font-weight: $font-weight-roobert-light;

			@include bp(sm) {
				font-size: 20px;
			}
		}
	}

	.Author {
		position: relative;

		.imgContainer {
			width: 79px;
			margin-right: 20px;
			position: relative;

			&:before {
				height: calc(100% + 8px);
				width: calc(100% + 8px);
				content: "";
				position: absolute;
				border: 1px solid rgba(#494444, 0.2);
				top: -5px;
				left: -5px;
				border-radius: 50%;
			}

			.imageInner {
				padding-bottom: 100%;
				width: 100%;
				border-radius: 50%;
				overflow: hidden;
				position: relative;
			}
		}

		img {
			opacity: 1;
		}
	}

	.ArticleBlockAuthor {
		//margin-bottom: 30px;

		@include bp(md) {
			//margin-bottom: 60px;
		}

		.Author {
			display: flex;
			align-items: center;
			margin-bottom: 30px;

			@include bp(md) {
				margin-bottom: 40px;
			}
		}

		.AuthorName {
			font-size: 14px;
			line-height: 1.5em;
			color: #494444;
			margin-bottom: 0;

			@include bp(md) {
				font-size: 16px;
			}
		}

		.AuthorTitle {
			font-size: 11px;
			line-height: 1.5em;
			color: #78746d;
			margin-bottom: 0;


			@include bp(md) {
				font-size: 14px;
			}
		}
	}

	.Block {
		h2 {
			color: #2f2425;
			font-size: 24px;
			line-height: 1.2em;
			font-weight: $font-weight-roobert-light;
			margin-bottom: 16px;
			@include poly-fluid-sizing("font-size", ($BREAKPOINT_MOBILE_BIG: 24px, $BREAKPOINT_DESKTOP_BIG: 30px));

			@include bp(sm) {
				margin-bottom: 20px;
				margin-top: 40px;
			}
		}

		h3 {
			font-size: 22px;
			line-height: 1.36em;
			margin-bottom: 16px;
			font-weight: 400;
			margin-top: 36px;
		}


		p, li, a {
			color: #78746d;
			font-size: 15px;
			line-height: 1.75em;

			@include bp(sm) {
				font-size: 16px;
			}
		}

		b, strong {
			font-weight: $font-weight-roobert-medium;
			color: #2f2425;
		}

	}

	.ArticleBlockText {
		margin-top: 40px;
		margin-bottom: 40px;

		p {
			margin-bottom: 20px;

			&:last-of-type {
				margin-bottom: 0;
			}
		}

		h3 {
			color: #2f2425;
			font-size: 22px;
			line-height: 1.2em;
		}

		ol {
			list-style: none;
			margin-bottom: 20px;
			counter-reset: ordered-list;

			li {
				counter-increment: ordered-list;
				position: relative;
				padding-left: 30px;

				&:before {
					content: counter(ordered-list) ".";
					margin-right: 18px;
					flex-shrink: 0;
					position: absolute;
					top: 0;
					left: 0;
					color: #fa3a00;
					font-size: 16px;
					font-family: $FONT;
				}
			}
		}

		ul {
			list-style: none;
			margin-bottom: 20px;

			li {
				position: relative;
				display: flex;
				margin-bottom: 6px;

				&:before {
					content: "";
					height: 4px;
					width: 4px;
					display: block;
					background: #fa3a00;
					transform: rotateZ(45deg);
					margin-right: 18px;
					flex-shrink: 0;
					position: relative;
					top: 13px;
				}
			}
		}

		a {
			position: relative;
			display: inline-block;
			color: #797672;
			transition: color .3s;

			&:before {
				content: '';
				width: 100%;
				position: absolute;
				bottom: 0px;
				left: 0;
				height: 1px;
				background: #fa3a00;
			}

			&:hover {
				color: #2f2425;
			}
		}


		img {
			opacity: 1;
			position: relative;
		}
	}

	.ArticleBlockQuote {
		margin-bottom: 64px;
		margin-top: 64px;

		.Separator {
			margin-bottom: 26px;
		}

		.quote {
			font-size: 24px;
			color: #494444;
			line-height: em(32px, 24px);
			font-weight: $font-weight-roobert-light;
			position: relative;
			@include poly-fluid-sizing("font-size", ($BREAKPOINT_MOBILE_BIG: 20px, $BREAKPOINT_DESKTOP_BIG: 24px));
			margin-bottom: 32px;

			@include bp(md) {
				margin-bottom: 0px;
			}

			&:before {
				content: "";
				position: absolute;
				top: 0;
				left: -12px;
			}

			&:after {
				content: "";
			}
		}

		.Author {
			display: flex;
			margin-bottom: 36px;
			align-items: center;

			p {
				font-size: 9px;
				line-height: 1.45em;
				letter-spacing: 0.12em;
				margin-bottom: 0;
				@extend .uppercase;

				@include bp(sm) {
					font-size: 11px;
				}
			}

			.AuthorName {
				color: #494444;
				font-family: $font-rogan;
				font-weight: $font-weight-rogan-bold;
			}

			.AuthorJobTitle {
				color: #78746d;
				font-family: $font-rogan;
				font-weight: $font-weight-rogan-bold;
				margin-left: 30px;
				position: relative;

				&:before {
					content: "";
					height: 6px;
					width: 6px;
					border: 1px solid #78746d;
					transform: rotateZ(45deg);
					position: absolute;
					box-sizing: border-box;
					left: -19px;
					top: 4px;
				}
			}
		}

	}

	.ArticleBlockImage {
		margin-top: 40px;
		margin-bottom: 40px;

		.ImageCaption {
			color: #564c4d;
			font-size: 11px;
			line-height: 1.45em;
			margin-top: 22px;
		}

		.imageContainer {
			cursor: pointer;

			&:before {
				height: calc(100% + 8px);
				width: calc(100% + 8px);
				content: "";
				position: absolute;
				border: 1px solid rgba(#494444, 0.2);
				top: -5px;
				left: -5px;
			}
		}
	}

	.PressContactModule {
		margin-top: 64px;
		margin-bottom: 64px;

		.Separator {
			margin-bottom: 56px;
		}

		h2 {
			margin-bottom: 20px;
		}

		a {
			color: #ed3915;
			text-transform: uppercase;
			font-size: 12px;
			line-height: 1.3em;
			font-family: $font-rogan;
			font-weight: $font-weight-rogan-bold;
			letter-spacing: 0.24em;
			margin-top: 20px;
			display: inline-block;
		}
	}

	.ArticleBlockVideo {
		.VideoCaption {
			color: #564c4d;
			font-size: 11px;
			line-height: 1.45em;
			margin-top: 22px;
		}
	}

	.ArticleBlockCodeEmbed {
		pre {
			white-space: pre-wrap;
		}
	}

	.PrevNextArticle {
		padding-top: 50px;
		padding-bottom: 200px;
		position: relative;

		@include bp(sm) {
			padding-bottom: 50px;
		}


		@include bp(md) {
			padding-top: 150px;
			padding-bottom: 0;
		}

		.BackgroundSvg {
			width: 100%;
			position: absolute;
			top: 10vh;
			left: 0;
			opacity: 1;

			path:nth-child(2) {
				display: none;
				@include bp(sm) {
					display: block;
				}
			}

			@include bp(sm) {
				top: auto;
				bottom: -50px;
				opacity: 0.6;
			}
		}

		.ArticleNav {
			width: 15vw;
			height: 15vw;
			min-width: 150px;
			min-height: 150px;
			max-width: 245px;
			max-height: 245px;
			display: block;
			position: relative;

			@include bp(sm) {
				left: -20px
			}

			@include bp(md) {
				left: 0;
			}

			&:after,
			&:before {
				content: "";
				border-radius: 50%;
				position: absolute;
				height: 100%;
				width: 100%;
				top: 0;
				left: 0;
				border: 1px solid $COLOR_RED;
				box-sizing: border-box;
				transition: transform .2s;
			}

			&:before {
				height: calc(100% + 12px);
				width: calc(100% + 12px);
				top: -6px;
				left: -6px;

				@include bp(md) {
					height: calc(100% + 14px);
					width: calc(100% + 14px);
					top: -7px;
					left: -7px;
				}
			}

			&:after {
				height: calc(100% + 24px);
				width: calc(100% + 24px);
				top: -12px;
				left: -12px;
				border-color: rgba($COLOR_RED, 0.2);

				@include bp(md) {
					height: calc(100% + 28px);
					width: calc(100% + 28px);
					top: -14px;
					left: -14px;
				}
			}

			.dash {
				position: absolute;
				width: 60px;
				height: 1px;
				background: $COLOR_RED;
				top: 14px;
				left: -70px;
				transform-origin: 100% 100%;
				display: inline-block;


				&:before {
					content: "";
					height: 3px;
					width: 3px;
					position: absolute;
					right: -2px;
					top: -2px;
					border-radius: 50%;
					border: 1px solid $COLOR_RED;
				}
			}

			@include bp(md) {
				&:hover {
					&:before {
						transform: scale(1.072);
						border-width: 2px;
					}

					&:after {
						transform: scale(0.93);
					}

					&.PrevArticle:after {
						transform: scale(0.91);
					}

					&.NextArticle:after {
						transform: scale(0.96);
					}
				}
			}

			&.PrevArticle {
				margin-top: 280px;
				margin-left: auto;
				width: 8vw;
				height: 8vw;
				display: none;

				@include bp(sm) {
					display: block;
				}


				@include bp(md) {
					top: -30px;
				}

				&:after,
				&:before {
					border-color: #78746d;
				}

				&:after {
					border-color: rgba(#78746d, 0.2);
				}

				.content {
					min-width: 180px;
					width: 11vw;
					right: auto;
					//top: 40%;
					@include poly-fluid-sizing("left", ($BREAKPOINT_TABLET_LANDSCAPE: 30px, $BREAKPOINT_DESKTOP_BIG: 0));
					transform: translateX(calc(-100% - 64px));
					text-align: right;
				}

				.dash {
					background: #78746d;
					width: 90px;
					right: -110px;
					left: auto;
					transform-origin: 0% 0%;
					transform: rotateZ(30deg);
					top: 11px;

					&:before {
						border-color: #78746d;
						left: -2px;
					}
				}
			}

			&.NextArticle {

				.content {
					top: calc(100% + 42px);
					left: 16vw;

					@include bp(md) {
						left: auto;
						top: 0;
						transform: translateX(calc(100% + 84px));
						@include poly-fluid-sizing("right", ($BREAKPOINT_TABLET_LANDSCAPE: 50px, $BREAKPOINT_DESKTOP_BIG: 0));
					}

					.text-14 {
						display: block;
						overflow: hidden;
						text-overflow: ellipsis;
						display: -webkit-box;
						-webkit-line-clamp: 4;
						-webkit-box-orient: vertical;
						width: 100%;
						margin-top: 10px;
					}

				}

				.dash {
					top: -11px;
					transform: rotateZ(105deg);
					width: 80px;
					left: -106px;

					@include bp(md) {
						width: 130px;
						left: -150px;
						top: 11px;
						transform: rotateZ(-20deg);
					}
				}
			}

		}

		.content {
			position: absolute;
			width: 200px;
			z-index: 8;

			@include bp(md) {
				width: 15vw;
				max-width: 270px;
				min-width: 245px;
			}

			a {
				text-decoration: none;
			}

			p:nth-of-type(1) {
				margin-bottom: 4px;
			}

			p:nth-of-type(2) {
				@extend .text-16;
				color: #322728;
				position: relative;
			}

			p:nth-of-type(3) {
				color: rgba(#494444, 0.6);
			}
		}

		.imageContainer {
			width: 100%;
			border-radius: 50%;
			overflow: hidden;
			position: relative;
			z-index: 6;

			.imageInner {
				padding-bottom: 100%;
			}

			img {

			}

		}

	}
}
