.TestModule {
	padding-left: 50px;
	padding-top: 50px;
	min-height: 100vh;
	padding-bottom: 130px;

	h3 {
		margin-bottom: 20px;
	}

	a {
		text-decoration: underline;
	}

	a:first-of-type {
		margin-right: 10px;
	}

	.NewsMainCategories {
		max-width: 232px;

		.NewsCategory {
			.MainCategoryTitle {
				color: $COLOR_TEXT;
				font-size: 20px;
				line-height: 1.4em;
				padding: 25px 0 15px 0;
				border-bottom: 1px solid rgba(#524d45, 0.2);
			}

			.NewsSubCategories {
				list-style: none;
				padding-bottom: 20px;

				li {
					font-size: 14px;
					color: rgba($COLOR_TEXT, 0.6);
					letter-spacing: 0.02em;
					padding: 22px 0 0 0;
				}
			}

			.ArticleCategory {
				display: flex;
				justify-content: space-between;
				flex-wrap: wrap;
			}

			.ArticlesAmount {
				font-family: $font-rogan;
				font-weight: $font-weight-rogan-bold;
				font-size: 12px;
			}

			.Separator {
				margin-top: 16px;
			}
		}
	}

	.articles {
		margin-top: 20px;

		.ArticlePreview {
			width: 100%;
			display: flex;
			text-decoration: none;
			padding: 25px 0;
			border-bottom: 1px solid rgba(#78746d, 0.2);

			&:last-child {
				border-bottom: 0;
			}

			.ArticleThumbnail {
				width: 316px;
				flex-shrink: 0;
				padding: 5px;
				border: 1px solid rgba(#78746d, 0.2);
			}

			.imageContainer {
			}

			.ArticlePreviewContent {
				width: calc(100% - 316px);
				padding-left: 32px;

				.ContentTopBar {
					display: flex;
					align-items: center;
					justify-content: space-between;
					margin-bottom: 24px;

					p {
						font-family: $font-rogan;
						font-weight: $font-weight-rogan-bold;
						color: rgba($COLOR_TEXT, 0.6);
						letter-spacing: 0.24em;
						text-transform: uppercase;
						line-height: 1.333em;
						font-size: 12px;
					}
				}

				.ArticleTitle {
				}
			}
		}
	}
}
