@import 'ShowroomItem/ShowroomItem';
html.ShowroomTemplatePage {
	body {
		overscroll-behavior-y: none;
		overscroll-behavior-x: none;
		background-image: none;
	}
}

:root {
	--showroomXGap: 5vw;
}

.ShowroomModule {
	.moduleInner {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100vh;
		min-height: unset;
		z-index: 0;
	}

	.Showroom {
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		z-index: 6;

		&:after {
			content: '';
			position: absolute;
			display: block;
			pointer-events: none;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			background-image: url('/assets/images/noisy-test2.png'), radial-gradient(rgba(209, 200, 185, 0) 60%, rgba(209, 200, 185, 1) 100%);
			z-index: 2000;
			transform: translateZ(10000px);
		}

		.PerspectiveContainer {
			height: 100%;
			width: 100%;
			position: relative;
			display: flex;
			align-items: center;
			justify-content: flex-start;
			perspective: 2700px;
			perspective-origin: 50% 0%;
			//perspective-origin: 50% 10%;
			transform-style: preserve-3d;
			pointer-events: none;
			@include bp(tp) {
				perspective: 1500px;
				justify-content: center;
			}
		}
	}

	.FilterList {
		position: fixed;
		bottom: 20px;
		right: 20px;

		list-style: none;
		z-index: 2001;
		transform: translateZ(10000px);
		@include bp(tp) {
			bottom: 35px;
			right: 35px;
		}
		@include bp(mds) {
			bottom: 35px;
			right: 42px;
		}
		@include bp(dp) {
			bottom: 52px;
			right: 60px;
		}

		&:hover {
			.FilterItem.is-active a {
				//border-bottom: 1px solid rgba(#78746d, 0.2);
				//
				//@include bp(tp) {
				//	border-bottom: 1px solid $COLOR_RED;
				//}

				&:before {
					opacity: 0;
				}
			}

			.FilterItem a:hover {
				border-color: $COLOR_RED;

				&:before {
					opacity: 1;

					@include bp(tp) {
						opacity: 0;
					}
				}
			}
		}

		.FilterItem {
			text-align: right;
			margin-top: 10px;
			position: relative;
			padding-right: 20px;
			display: none;

			@include bp(tp) {
				display: block;
			}

			&:before {
				@include bp(tp) {
					content: '';
					height: 3px;
					width: 3px;
					background: $COLOR_BLACK;
					position: absolute;
					right: 0;
					top: 50%;
					transform: translateY(-50%) rotateZ(45deg);
				}
			}

			&:after {
				content: '';
				height: 7px;
				width: 7px;
				border: 1px solid rgba(#78746d, 0.3);
				position: absolute;
				right: -3px;
				top: 50%;
				transform: translateY(-50%) rotateZ(75deg);
				opacity: 0;
				transition: opacity 0.3s, transform 0.3s 0.05s;
			}

			// Mobile dropdown: triangle icon
			a:before {
				content: '';
				position: absolute;
				right: -16px;
				top: 5px;
				width: 0;
				height: 0;
				border-left: 4px solid transparent;
				border-right: 4px solid transparent;
				border-top: 4px solid #000;
				transition: opacity 0.3s;
				opacity: 0;
			}

			&.is-active {
				display: block;

				a {
					border-color: $COLOR_RED;

					&:before {
						opacity: 1;

						@include bp(tp) {
							opacity: 0;
						}
					}
				}
			}

			&:hover:not(.is-active) {
				a {
					border-color: $COLOR_RED;
					//
					//&:before {
					//	display: block;
					//}
				}

				@include bp(tp) {
					a:before {
						display: none;
					}

					&:after {
						transform: translateY(-50%) rotateZ(45deg);
						opacity: 1;
					}

					a {
						border-color: rgba(#78746d, 0.6);
					}
				}
			}

			a {
				@extend .uppercase;
				font-size: 11px;
				color: #262222;
				border-bottom: 1px solid rgba(#78746d, 0.2);
				padding-bottom: 4px;
				transition: border 0.3s;
				position: relative;
				display: inline-block;
			}
		}
	}

	.CenterPoint {
		position: relative;
		transform-style: inherit;
		pointer-events: none;
	}

	.ShowroomItem {
		width: 90vw;
		position: absolute;
		//bottom: -50vh;
		top: 100px;
		will-change: transform;
		transform-origin: bottom;
		transform: translate3d(0, 0, -200px);
		transform-style: preserve-3d;
		perspective: 3000px;
		perspective-origin: center;
		left: 5vw;
		user-select: none;
		cursor: pointer;
		pointer-events: auto;
		@media (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
			top: -130px;
			//bottom: -130px;
		}
		@media (min-width: 640px) and (max-height: 630px) {
			top: 0px;
			//bottom: 0px;
		}

		@include bp(tp) {
			left: auto;
			right: var(--showroomXGap);
			width: 50vw;
		}
		@media (min-width: 1440px) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
			top: -100px;
			//bottom: -100px;
		}
		.ShowroomInner {
			position: relative;
			will-change: transform;
			contain: strict;
			transform-style: preserve-3d;
			img {
				pointer-events: none;
				border: 4px solid $COLOR_BEIGE;
				outline: 1px solid #d3ceca;
			}
		}
		&:nth-child(2n) {
			right: auto;
			left: var(--showroomXGap);
		}
	}

	.ScrollSpacer {
		display: block;
		position: relative;
		height: 100vh;
		z-index: -1;
	}

	.Showroom-scrollIndicator {
		position: fixed;
		left: 22px;
		left: calc(22px + env(safe-area-inset-left));
		top: calc(50% - (100vh - 200px) * 0.5);
		height: 300px;
		max-height: calc(100vh - 200px);

		@media (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
			top: calc(50% - 150px);
		}

		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
			left: calc(#{$TABLET_MARGIN} + env(safe-area-inset-left));
		}
		@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: $BREAKPOINT_HEIGHT) {
			left: calc(60px + env(safe-area-inset-left));
		}

		.Showroom-scrollIndicator-lineBG {
			content: '';
			position: absolute;
			height: 100%;
			top: 0px;
			width: 2px;
			opacity: 0.7;
			background-image: url("data:image/svg+xml,%3Csvg width='2' height='2' viewBox='0 0 2 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='1' cy='1' r='1' fill='%2378746D'/%3E%3C/svg%3E%0A");
			@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_HEIGHT) {
				opacity: 1;
				background-image: url("data:image/svg+xml,%3Csvg width='2' height='2' viewBox='0 0 2 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='1' cy='1' r='1' fill='%2388837a'/%3E%3C/svg%3E%0A");
			}
			background-size: 2px 6px;
			background-repeat: repeat-y;
			transform-origin: center top;
			will-change: transform;
		}

		.Showroom-scrollIndicator-line {
			transform-origin: center top;
			position: absolute;
			top: -6px;
			width: 2px;
			height: calc(100% + 2px);
			background: $COLOR_ORANGE;
			transform: scaleY(0);
			will-change: transform;
		}

		.Showroom-Diamond {
			position: absolute;
			left: -4px;
			top: -15px;
			width: 10px;
			height: 10px;
			display: block;
			transform: rotateZ(45deg);
			border: 2px solid $COLOR_ORANGE;

			&:last-child {
				top: auto;
				bottom: -11px;
				border: 2px solid rgba(51, 47, 42, 0.2);
			}
		}
	}
}
