@keyframes rotateForever {
	0% {
		transform: rotateZ(0deg);
	}
	100% {
		transform: rotateZ(360deg);
	}
}

@keyframes rotateForeverReverse {
	0% {
		transform: rotateZ(0deg);
	}
	100% {
		transform: rotateZ(-360deg);
	}
}

$desktopPadding: 60px;
$tabletPadding: 35px;
$openEase: $ease-out-quint;
$closeEase: $ease-in-out-quint;
$openDuration: 1s;
$closeDuration: 1s;
$opacityTransitionOpen: opacity 0.5s linear;
$opacityTransitionClose: opacity 0.3s #{1-1/2}s linear;
:root {
	--menu-offset: 0px;
	--menu-offset-overlay: 3vw;
	@include bp(md) {
		--menu-offset-overlay: 80px;

		--menu-circle-nav-diamond-offset: 20px;
	}
}

.MainMenu-CoverLayer {
	position: fixed;
	top: 0;
	left: 0;
	display: flex;
	width: 100vw;
	height: 100%;
	max-height: 100vh;
	background-image: url('data:image/png;base64,'),
		linear-gradient(180deg, #f2f2f2 33%, #e9e5e1 100%);
	background-size: 30px 30px, 100%;
	background-repeat: repeat, no-repeat;
	z-index: 100000;
	pointer-events: none;
	opacity: 1;
	visibility: visible;
	transform: translate3d(0, 0, 0);
	will-change: transform;
	contain: strict;
	align-items: center;
	justify-content: center;

	svg {
		width: 154px;
		height: 239px;
	}
}

.MainMenu-Background-overlay {
	position: fixed;
	top: calc(-120vw * 0.5 + var(--menu-offset-overlay));
	left: calc(-120vw * 0.5 + var(--menu-offset-overlay));
	width: 120vw;
	height: 120vw;
	@include bp(mds) {
		top: calc(-37vw * 0.5 + var(--menu-offset-overlay));
		left: calc(-37vw * 0.5 + var(--menu-offset-overlay));
		width: 37vw;
		height: 37vw;
	}
	display: block;
	will-change: transform;
	transform: scale3d(12, 12, 1) translate3d(0, 0, 0);
	z-index: 100000;
	contain: strict;

	&:after {
		content: '';
		position: absolute;
		width: calc(52% - 2vw);
		height: calc(52% - 2vw);
		border-radius: 50%;
		display: block;
		top: calc((100% - 52%) * 0.5 + 1vw);
		left: calc((100% - 52%) * 0.5 + 1vw);
		box-shadow: $COLOR_BEIGE 1vw 0.1vw 1vw 1vw, $COLOR_BEIGE -1vw 0.1vw 1vw 1vw;
		background: $COLOR_BEIGE;
		z-index: -1;
		animation: rotateForever 100s linear infinite;
	}

	img {
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		opacity: 1;
		width: 100%;
		height: 100%;
		animation: rotateForever 100s linear infinite;
	}

	pointer-events: none;
	opacity: 0;
}

.MainMenu-Background-circles {
	position: fixed;
	width: 170vw;
	height: 170vw;
	left: calc(-170vw * 0.5 + var(--menu-offset));
	top: calc(-170vw * 0.5 + var(--menu-offset));
	display: none;
	pointer-events: none;
	opacity: 0;
	transform: scale3d(12, 12, 1) translate3d(0, 0, 0);
	will-change: transform;
	z-index: 100000;
	line-height: 0;
	contain: strict;

	img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;

		&.mobile {
			display: none;
		}
	}

	@media (max-width: $BREAKPOINT_TABLET_PORTRAIT - 1) {
		width: 280vw;
		height: 280vw;
		left: calc(-280vw * 0.5 + var(--menu-offset));
		top: calc(-280vw * 0.5 + var(--menu-offset));
		display: none;
		img.mobile {
			display: block;
		}
		img.desktop {
			display: none;
		}
	}
}

.MainMenu-Background {
	position: fixed;
	left: 0;
	top: 0;
	display: block;
	width: 100%;
	height: 100vh;
	pointer-events: none;
	transform-origin: 0 0;
	opacity: 0;
	will-change: transform;
	transform: scale3d(12, 12, 1) translate3d(0, 0, 0);
	z-index: 100000;
	background-color: $COLOR_BEIGE;
	background-image: radial-gradient(transparent 50%, rgba(0, 0, 0, 0.85) 150%), linear-gradient(-55deg, #122330 15%, #431e2e 100%);
	contain: strict;
}

html.is-safari {
	.MainMenu-Background {
		background-image: radial-gradient(transparent 50%, rgba(0, 0, 0, 0.85) 150%), url('/assets/svg/menu_circles_quarter.png'), linear-gradient(-55deg, #122330 15%, #431e2e 100%);
		background-repeat: no-repeat;
		background-size: 100%, 85.7vw, 100%;
	}
}

.Menu-FadeIn {
	opacity: 0;
}

.MainMenu-TopCTA {
	opacity: 0;

	& > * {
		pointer-events: none;
	}
}

.MainMenu-CoverLayer {
	transition: opacity 0.5s 0s linear, visibility 0s 0.5s;
}

html.page-changing {
	.MainMenu-CoverLayer {
		transition: opacity 0.5s 0s linear, visibility 0s 0s;
	}

	.MainMenu-TopCTA {
		opacity: 0 !important;
		transition: opacity 0.2s 0s linear !important;
	}
}

html.not-safari {
	.MainMenu-Background-circles {
		display: block;

		img {
			animation: rotateForever 150s linear infinite;
		}
	}
}

html.site-loaded {
	.MainMenu-TopCTA {
		will-change: opacity;
		transition: opacity 0.2s #{1-1/2}s linear;
	}

	.Menu-FadeIn {
		will-change: opacity;
		transition: opacity 0.2s 0s linear;
	}

	@include bp(menuCircles) {
		nav.Menu-FadeIn {
			will-change: unset;
			transition: none !important;
			opacity: unset !important;
		}
		nav {
			a {
				will-change: transform;
				transform-origin: left center;
				transform: translate3d(255vw, 255vw, 0px) rotateZ(570deg);
				transition: opacity 0.2s 0s linear, transform $closeDuration $closeEase;
				opacity: 0;
			}
		}
	}

	//Works around a chrome bug that would otherwise animate the menu to it's closed state on page load.
	.MainMenu-Background-overlay {
		transition: transform $closeDuration $closeEase, $opacityTransitionClose;
	}

	.MainMenu-Background-circles {
		transition: transform $closeDuration $closeEase, $opacityTransitionClose;
	}

	.MainMenu-Background {
		transition: transform $closeDuration $closeEase, $opacityTransitionClose;
	}
}

html.menu-open {
	.MainMenu-TopCTA {
		opacity: 1;
		transition: opacity 0.1s 0s linear;
	}

	.MainMenu-CoverLayer {
		//transition: opacity 0s 0.5s linear;
		transition: opacity 0s 0.5s linear, visibility 0s 0.5s;
	}

	.MainMenu-Background {
		transition: transform $openDuration $openEase, $opacityTransitionOpen;
		opacity: 1;
		transform: scale3d(1, 1, 1) translate3d(0, 0, 0);
	}

	.Menu-FadeIn {
		opacity: 1;
		transition: opacity 0.5s 0.5s linear;
	}

	@include bp(menuCircles) {
		nav.Menu-FadeIn {
			will-change: unset;
			transition: none !important;
			opacity: unset !important;
		}
		nav {
			a {
				transform: translate3d(0vw, 0vw, 0px) rotateZ(0deg);
				opacity: 1;
				@for $i from 1 through 8 {
					&:nth-child(#{$i}) {
						transition: opacity 0.5s #{0.5s + $i * 0.095s} linear, transform $openDuration * 1.2 $openEase;
					}
				}

				.MainMenu-number {
					transition: 0.3s;
					transform: translateX(0px);
				}

				.MainMenu-itemTitle {
					transition: 0.3s;
					transform: translateX(0px);
				}

				@media (hover: hover) {
					&:hover {
						.MainMenu-number {
							transition: 0.3s;
							transform: translateX(5px);
						}

						.MainMenu-itemTitle {
							transition: 0.3s;
							transform: translateX(10px);
						}

						.MainMenu-circleDiamond {
							.innerDiamond {
								transition: 0.3s;
								transform: scale(0.75) !important;
								//opacity: 0 !important;
							}

							.outerDiamond {
								transition: 0.3s;
								transform: scale(1) !important;
								opacity: 0;
							}

							.highlightDiamond {
								transition: 0.3s;
								opacity: 1 !important;
								transform: scale(1) !important;
							}

							.highlightDiamondOuter {
								transition: 0.3s;
								opacity: 0.2 !important;
								transform: scale(1) !important;
							}
						}
					}
				}
			}
		}
	}

	.MainMenu-Background-circles {
		transition: transform $openDuration * 1.2 $openEase, $opacityTransitionOpen;
		opacity: 1;
		transform: scale3d(1, 1, 1) translate3d(0, 0, 0);
	}

	.MainMenu-Background-overlay {
		transition: transform $openDuration $openEase, $opacityTransitionOpen;
		opacity: 1;
		transform: scale3d(1, 1, 1) translate3d(0, 0, 0);
	}
}

.MainMenu {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	padding-left: 20px;
	padding-right: 20px;
	padding-bottom: 50px;
	padding-top: 140px;
	z-index: 100001;
	overflow: auto;
	overscroll-behavior: contain;
	visibility: hidden;
	background-image: url('/assets/images/menu_noise.png');
	display: flex;
	align-items: center;
	justify-content: flex-start;
	flex-flow: column;
	contain: strict;

	@include bp(mds) {
		justify-content: center;
		padding-left: 35px;
		padding-right: 35px;
		padding-bottom: 35px;
		padding-top: 35px;
	}
	@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) {
		padding-left: $desktopPadding;
		padding-right: $desktopPadding;
		padding-bottom: $desktopPadding;
		padding-top: $desktopPadding;
	}

	.MainMenu-diamonds {
		display: flex;
		flex-flow: row;
		margin-right: 6px;
		align-items: center;

		&:after,
		&:before {
			content: '';
			display: block;
			width: 6px;
			height: 6px;
			box-sizing: border-box;
			border: 1px solid #ffffff;
			transform: rotateZ(-45deg);
			transition: opacity 0.1s linear, transform 0.2s $ease-out-quad;
		}

		&:after {
			opacity: 0.2;
			transform: translateX(-2px) rotateZ(-45deg);
		}
	}

	.MainMenu-circleDiamond {
		display: none;
		@include bp(menuCircles) {
			width: 4px;
			height: 4px;
			display: block;
			transform: rotateZ(45deg);
			position: absolute;
			left: 0;
			top: 0;
			@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: $BREAKPOINT_HEIGHT) {
				width: 6px;
				height: 6px;
			}

			.outerDiamond {
				width: 11px;
				height: 11px;
				display: block;
				position: absolute;
				box-sizing: content-box;
				top: -4px;
				left: -4px;
				will-change: transform;
				@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: $BREAKPOINT_HEIGHT) {
					width: 14px;
					height: 14px;
					top: -6px;
					left: -6px;
				}
				transition: 0.3s;
				z-index: -1;
				border: 1px solid rgba(255, 255, 255, 0.3);
				//opacity: 0.2;
			}

			.innerDiamond {
				width: 4px;
				height: 4px;
				background: #ffffff;
				display: block;
				position: absolute;
				box-sizing: content-box;
				z-index: -1;
				top: 0px;
				left: 0px;
				will-change: transform;
				transition: 0.3s;
				transform: scale(1);
				//opacity: 0.2;
				@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: $BREAKPOINT_HEIGHT) {
					width: 5px;
					height: 5px;
				}
			}

			.highlightDiamond {
				width: 10px;
				height: 10px;
				display: block;
				position: absolute;
				box-sizing: content-box;
				top: -5px;
				left: -5px;
				opacity: 0;
				will-change: transform;
				@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: $BREAKPOINT_HEIGHT) {
					width: 15px;
					height: 15px;
					top: -7px;
					left: -7px;
				}

				transition: 0.3s;
				transform: scale(0.75);
				z-index: -1;
				border: 2px solid $COLOR_ORANGE;
			}

			.highlightDiamondOuter {
				width: 16px;
				height: 16px;
				display: block;
				position: absolute;
				box-sizing: content-box;
				top: -7px;
				left: -7px;
				opacity: 0;
				transition: 0.3s;
				will-change: transform;
				transform: scale(0.5);
				@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: $BREAKPOINT_HEIGHT) {
					width: 25px;
					height: 25px;
					top: -11px;
					left: -11px;
				}

				z-index: -1;
				border: 1px solid $COLOR_ORANGE;
			}
		}
	}

	.MainMenu-itemTitle {
		display: flex;
		flex-flow: row;
		justify-content: center;
		align-items: center;

		.MainMenu-diamonds {
			margin-right: 21px;
			margin-left: 21px;
			margin-top: 5px;
			transition: transform 0.8s $ease-out-quad;
			transform: translateX(10px);

			@include bp(menuCircles) {
				display: none;
			}

			&:after,
			&:before {
				transition: opacity 0.3s linear, transform 0.6s $ease-out-quad;
				border-color: $COLOR_ORANGE;
			}

			&:before {
				width: 5px;
				height: 5px;
				opacity: 0;
				transform: translateX(7px) rotateZ(-45deg);
			}

			&:after {
				opacity: 0;
				width: 7px;
				height: 7px;
				transform: translateX(1px) rotateZ(-45deg);
			}

			&:last-child {
				position: relative;
				left: -1px;
				transform: translateX(-10px);

				&:after {
					width: 5px;
					height: 5px;
					opacity: 0;
					transform: translateX(-6px) rotateZ(-45deg);
				}

				&:before {
					opacity: 0;
					width: 7px;
					height: 7px;
					transform: translateX(0px) rotateZ(-45deg);
				}
			}
		}
	}

	.MainMenu-secondaryLinks {
		text-align: center;
		width: 100%;
		margin-bottom: 32px;
		@include bp(tp) {
			max-width: 304px;
		}
		@include bp(mds) {
			text-align: left;
			position: absolute;
			left: $tabletPadding;
			bottom: $tabletPadding;
			margin-bottom: 20px;
		}
		@include bp(dp) {
			left: $desktopPadding;
			bottom: $desktopPadding;
		}

		.MainMenu-Documents {
			width: 100%;
			height: 56px;
			font-family: $font-roobert;
			font-weight: $font-weight-roobert-regular;
			color: #ffffff;
			letter-spacing: 0.3px;
			line-height: 16px;
			font-size: 12px;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-shrink: 0;
			pointer-events: auto;
			cursor: pointer;

			& > * {
				pointer-events: none;
			}

			& > div {
				display: flex;
				flex-flow: row;

				& > span {
					transition: 0.2s;
				}
			}
			@media (hover: hover) {
				&:hover {
					span {
						transform: translateX(3px);
					}

					.MainMenu-diamonds {
						&:before {
							transform: translateX(1.5px) rotateZ(-45deg);
						}

						&:after {
							transform: translateX(-4px) rotateZ(-45deg);
						}
					}
				}
			}

			@include bp(tp) {
				height: auto;
				width: auto;
				flex-grow: 0;
				align-items: flex-start;
				flex-flow: column;
				padding-bottom: 17px;
				margin-bottom: 16px;
			}

			&:first-child {
				border-top: 1px solid rgba(#ffffff, 0.2);
				@include bp(tp) {
					border: none;
				}
			}

			@include bp(tp) {
				&:last-child {
					&:after {
						content: none;
					}
				}
			}

			p {
				display: none;
				font-size: 12px;
				color: rgba(#ffffff, 0.5);
				line-height: 1.667;
				letter-spacing: 0.04em;
				@include bp(tp) {
					margin-top: 3px;
				}
				@include bp(mds) {
					display: block;
				}
			}
		}

		a {
			width: 100%;
			height: 56px;
			font-family: $font-roobert;
			font-weight: $font-weight-roobert-regular;
			color: #ffffff;
			letter-spacing: 0.3px;
			line-height: 16px;
			font-size: 12px;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-shrink: 0;

			& > * {
				pointer-events: none;
			}

			& > div {
				display: flex;
				flex-flow: row;

				& > span {
					transition: 0.2s;
				}
			}

			@media (hover: hover) {
				&:hover {
					span {
						transform: translateX(3px);
					}

					.MainMenu-diamonds {
						&:before {
							transform: translateX(1.5px) rotateZ(-45deg);
						}

						&:after {
							transform: translateX(-4px) rotateZ(-45deg);
						}
					}
				}
			}

			@include bp(tp) {
				height: auto;
				width: auto;
				flex-grow: 0;
				align-items: flex-start;
				flex-flow: column;
				padding-bottom: 17px;
				margin-bottom: 16px;
			}

			&:first-child {
				border-top: 1px solid rgba(#ffffff, 0.2);
				@include bp(tp) {
					border: none;
				}
			}

			@include bp(tp) {
				&:last-child {
					&:after {
						content: none;
					}
				}
			}

			p {
				display: none;
				font-size: 12px;
				color: rgba(#ffffff, 0.5);
				line-height: 1.667;
				letter-spacing: 0.04em;
				@include bp(tp) {
					margin-top: 3px;
				}
				@include bp(mds) {
					display: block;
				}
			}
		}
	}

	.borderBottomDotted {
		position: relative;
		width: 100%;
		display: block;

		&:after {
			content: '';
			opacity: 0.5;
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			height: 2px;
			background-image: url("data:image/svg+xml,%3Csvg width='2' height='2' viewBox='0 0 5 2' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect width='1' height='1' fill='%2390A3CF'/%3E%3C/svg%3E%0A");
			background-size: 5px 2px;
			background-repeat: repeat-x;
		}
	}

	.MainMenu-tertiaryLinks {
		text-align: center;
		display: flex;
		flex-flow: row;
		justify-content: center;
		align-items: center;
		@include bp(mds) {
			position: absolute;
			left: $tabletPadding;
			bottom: $tabletPadding;
		}
		@include bp(dp) {
			left: $desktopPadding;
			bottom: $desktopPadding;
		}

