					margin-right: 10px;
					margin-left: 8px;
					display: inline-block;
					background: #90a3cf;
				}
			}
		}
	}

	.MainMenu-listingLinks {
		width: 100%;
		max-width: 304px;
		text-align: center;
		margin-top: 10px;
		margin-bottom: 8px;

		&:after {
			content: '';
			opacity: 0.5;
			width: 100%;
			height: 2px;
			display: block;
			background-image: url("data:image/svg+xml,%3Csvg width='2' height='2' viewBox='0 0 5 2' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect width='1' height='1' fill='%2390A3CF'/%3E%3C/svg%3E%0A");
			background-size: 5px 2px;
			background-repeat: repeat-x;
			margin-top: 20px;
		}

		@include bp(mds) {
			display: flex;
			flex-flow: row;
			justify-content: center;
			align-items: center;
			width: auto;
			max-width: initial;
			margin-top: 0;
			margin-bottom: 0;

			&:after {
				content: none;
			}
		}

		@include bp(mds) {
			position: absolute;
			bottom: $tabletPadding;
			left: 50%;
			transform: translateX(-50%);
		}

		@include bp(dp) {
			bottom: $desktopPadding;
			left: 50%;
			transform: translateX(-50%);
		}

		.MainMenu-listingLink {
			text-transform: uppercase;
			font-family: $font-rogan;
			font-weight: $font-weight-rogan-bold;
			font-size: 10px;
			line-height: 14px;
			letter-spacing: 0.26em;
			display: flex;
			flex-flow: column;
			justify-content: center;
			align-items: center;
			margin-bottom: 5px;

			@include bp(tp) {
				font-size: 12px;
				line-height: 20px;
			}

			@include bp(mds) {
				margin-bottom: 0;

				&:not(:last-child) {
					.listingLinks {
						&:after {
							content: '';
							width: 2px;
							height: 2px;
							border-radius: 50%;
							margin-right: 10px;
							margin-left: 8px;
							display: inline-block;
							background: #90a3cf;
						}
					}
				}
			}

			span {
				color: #c8c8c8;
			}

			.listingLinks {
				color: #90a3cf;
				display: flex;
				flex-flow: row;
				justify-content: center;
				align-items: center;

				a {
					color: #90a3cf;
					opacity: 0.7;
					transition: opacity 0.2s;

					@media (hover: hover) {
						&:hover {
							opacity: 1;
						}
					}

					&:not(:last-child) {
						&:after {
							content: '/';
							width: 2px;
							height: 2px;
							margin-right: 8px;
							margin-left: 3px;
							display: inline-block;
						}
					}
				}
			}
		}
	}

	.MainMenu-TopCTA {
		position: absolute;
		height: 40px;
		z-index: 4;
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
			height: 50px;
		}
		top: 20px;
		right: calc(10px + env(safe-area-inset-right));
		@include bp(xxs) {
			right: calc(18px + env(safe-area-inset-right));
		}
		@media (min-width: $BREAKPOINT_TABLET_PORTRAIT) and (min-height: $BREAKPOINT_TABLET_MARGIN_HEIGHT) {
			right: calc(#{$TABLET_MARGIN} + env(safe-area-inset-right));
			top: $TABLET_MARGIN;
		}
		@media (min-width: $BREAKPOINT_DESKTOP_MARGIN) and (min-height: $BREAKPOINT_HEIGHT) {
			right: calc(60px + env(safe-area-inset-right));
			top: 40px + 20px;
		}
	}

	.MainMenu-newsletter {
		max-width: 304px;

		form {
			display: flex;
			flex-flow: column;
			align-items: center;
			@include bp(mds) {
				align-items: flex-end;
			}
		}

		label {
			text-align: center;
			@include bp(mds) {
				text-align: right;
			}
		}

		@include bp(mds) {
			&:after {
				content: none;
			}
			position: absolute;
			right: $tabletPadding;
			bottom: $tabletPadding;
			padding-bottom: 24px;
		}
		@include bp(dp) {
			max-width: 360px;
			right: $desktopPadding;
			bottom: $desktopPadding;
		}
	}

	nav {
		display: flex;
		flex-flow: column;
		margin-bottom: 64px;
		flex-shrink: 0;
		overflow: hidden;

		@include bp(menuCircles) {
			margin-bottom: 0;
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}

		a {
			font-family: $font-roobert;
			font-weight: $font-weight-roobert-light;
			color: #ffffff;
			text-align: center;
			display: flex;
			flex-flow: column;
			letter-spacing: 0.02em;
			@include poly-fluid-sizing(
				'font-size',
				(
					320px: 24px,
					768px: 36px
				)
			);
			@include poly-fluid-sizing(
				'line-height',
				(
					320px: 32px,
					768px: 44px
				)
			);
			@include poly-fluid-sizing(
				'margin-bottom',
				(
					320px: 12px,
					768px: 39px
				)
			);
			@include bp(menuCircles) {
				text-align: left;
				flex-flow: row;
				align-items: center;
				margin-bottom: 0;
				position: absolute;
				padding-left: var(--menu-circle-nav-diamond-offset);
				padding-top: var(--menu-circle-nav-diamond-offset);
				margin-left: calc(var(--menu-circle-nav-diamond-offset) * -1);
				margin-top: calc(var(--menu-circle-nav-diamond-offset) * -1);
				&:nth-child(1) {
					left: calc(85vw * 0.219 + var(--menu-offset) + var(--menu-circle-nav-diamond-offset));
					top: calc(85vw * 0.19 + var(--menu-offset) + var(--menu-circle-nav-diamond-offset));
				}
				&:nth-child(2) {
					left: calc(85vw * 0.252 * 2 + var(--menu-offset) + var(--menu-circle-nav-diamond-offset));
					top: calc(85vw * 0.14 + var(--menu-offset) + var(--menu-circle-nav-diamond-offset));
				}
				&:nth-child(3) {
					left: calc(85vw * 0.338 + var(--menu-offset) + var(--menu-circle-nav-diamond-offset));
					top: calc(85vw * 0.4 + var(--menu-offset) + var(--menu-circle-nav-diamond-offset));
				}
				&:nth-child(4) {
					left: calc(85vw * 0.7242 + var(--menu-offset) + var(--menu-circle-nav-diamond-offset));
					top: calc(85vw * 0.21 + var(--menu-offset) + var(--menu-circle-nav-diamond-offset));
				}
				&:nth-child(5) {
					left: calc(85vw * 0.6392 + var(--menu-offset) + var(--menu-circle-nav-diamond-offset));
					top: calc(85vw * 0.4 + var(--menu-offset) + var(--menu-circle-nav-diamond-offset));
				}
				&:nth-child(6) {
					left: calc(85vw * 0.955 + var(--menu-offset) + var(--menu-circle-nav-diamond-offset));
					top: calc(85vw * 0.29 + var(--menu-offset) + var(--menu-circle-nav-diamond-offset));
				}
				@include poly-fluid-sizing(
					'font-size',
					(
						1024px: 22px,
						1440px: 36px
					)
				);
				@include poly-fluid-sizing(
					'line-height',
					(
						1024px: 22px,
						1440px: 36px
					)
				);
			}

			&:last-child {
				margin-bottom: 0;
			}

			.MainMenu-number {
				opacity: 0.4;
				font-family: $font-rogan;
				font-weight: $font-weight-rogan-bold;
				@include poly-fluid-sizing(
					'font-size',
					(
						320px: 9px,
						768px: 11px
					)
				);
				@include poly-fluid-sizing(
					'line-height',
					(
						320px: 12px,
						768px: 16px
					)
				);
				color: #ffffff;
				letter-spacing: 0.24em;
				text-align: center;
				@include bp(menuCircles) {
					margin-top: 5px;
					margin-right: 10px;
					@include poly-fluid-sizing(
						'line-height',
						(
							320px: 9px,
							768px: 11px
						)
					);
				}
			}

			@media (hover: hover) {
				&:hover {
					.MainMenu-diamonds {
						transform: translateX(0px) !important;

						&:before {
							opacity: 0.2;
							transform: translateX(0px) rotateZ(-45deg);
						}

						&:after {
							opacity: 1;
							transform: translateX(1px) rotateZ(-45deg);
						}

						&:last-child {
							&:after {
								opacity: 0.2;
								transform: translateX(0px) rotateZ(-45deg);
							}

							&:before {
								opacity: 1;
								transform: translateX(0px) rotateZ(-45deg);
							}
						}
					}
				}
			}

			&.currentPage {
				.MainMenu-diamonds {
					transform: translateX(0px) !important;

					&:before {
						opacity: 0.2;
						transform: translateX(0px) rotateZ(-45deg);
					}

					&:after {
						opacity: 1;
						transform: translateX(1px) rotateZ(-45deg);
					}

					&:last-child {
						&:after {
							opacity: 0.2;
							transform: translateX(0px) rotateZ(-45deg);
						}

						&:before {
							opacity: 1;
							transform: translateX(0px) rotateZ(-45deg);
						}
					}
				}
			}
		}
	}

	.MenuSocials {
		text-align: center;
		position: relative;

		@include bp(sm) {
			text-align: right;
		}

		width: 100%;
		margin: 21px 0;
		@include bp(tp) {
			width: auto;
			margin: 0;
		}

		@include bp(mds) {
			position: absolute;
			right: $tabletPadding;
			bottom: $tabletPadding;
		}
		@include bp(dp) {
			right: $desktopPadding;
			bottom: $desktopPadding;
		}

		svg {
			will-change: opacity;
			transform: translate3d(0, 0, 0);
			pointer-events: none;
		}

		.SocialsList {
			display: flex;
			flex-flow: row;
			width: 100%;
			align-items: center;
			justify-content: space-between;
			position: relative;

			@include bp(sm) {
				padding-top: 0;
				border: none;
			}

			a {
				line-height: 0;
				opacity: 1;
				transition: opacity 0.2s;
				@include bp(tp) {
					padding: 10px;
				}
				@include bp(sm) {
					opacity: 0.6;

					&:hover {
						opacity: 1;
					}
				}
			}
		}
	}

	.MainMenu-CloseIcon {
		position: fixed;
		top: 0px;
		left: 0px;
		height: 12px;
		width: 12px;
		z-index: 8;
		cursor: pointer;
		line-height: 0;
		padding-top: 36px;
		padding-left: 36px;
		box-sizing: content-box;
		@include bp(xxs) {
			padding-top: 44px;
			padding-left: 44px;
		}
		@include bp(md) {
			padding-top: calc(#{$desktopPadding} + 28px);
			padding-left: calc(#{$desktopPadding} + 28px);
			//top: calc(var(--menu-offset) - 6px);
			//left: calc(var(--menu-offset) - 6px);
		}

		&:before,
		&:after {
			position: absolute;
			content: '';
			border: 2px solid #191e2a;
			transition: transform 0.5s;
			border-radius: 50%;
		}

		&:before {
			height: 50px;
			width: 50px;
			right: -21px;
			bottom: -21px;
			border: 1px solid rgba(#191e2a, 0.3);
			transform: scale(1, 1);
			transition: transform 0.2s;
			will-change: transform;
			@include bp(md) {
				height: 61px;
				width: 61px;
				right: -27px;
				bottom: -27px;
			}
		}

		svg {
			position: absolute;
			right: -1px;
			bottom: -2px;
			height: 12px;
			width: 12px;
			transform: scale(1, 1);
			will-change: transform;
			transition: transform 0.2s 0.05s;
		}

		&:after {
			height: 60px;
			width: 60px;
			right: -27px;
			bottom: -27px;
			@include bp(md) {
				height: 72px;
				width: 72px;
				right: -33px;
				bottom: -33px;
			}
		}

		@media (hover: hover) {
			&:hover {
				&:before {
					transform: scale(1.2, 1.2);
				}

				svg {
					transform: scale(1.2, 1.2);
				}
			}
		}
	}
}

#MainMenu-Documents-Modal {
	position: fixed;
	top: 50%;
	left: 50%;
	display: none;
	width: 0;
	height: 0;
	overflow: auto;
	z-index: 100005;
	pointer-events: auto;
	cursor: pointer;
	opacity: 0;
	visibility: hidden;
	transform: translate3d(0, 0, 0);
	will-change: transform;
	contain: strict;
	align-items: center;
	justify-content: center;
	background-color: rgb(0, 0, 0);
	background-color: rgba(0, 0, 0, 0.5);

	.download-svg-icon {
		display: none;
	}

	.modal-content {
		padding: 18px;
		width: 80vw;
		overflow: auto;
		cursor: default;
		background-color: rgb(255, 255, 255); /* Fallback color */
		background-color: rgba(255, 255, 255, 0.8); /* Black w/ opacity */
		max-height: 80vh;
		overflow-y: auto;
		position: fixed;

		@include bp(dp) {
			width: auto;
			min-width: 50vw;
			padding: $desktopPadding;
		}

		.modal-title {
			width: 100%;
			font-family: $font-roobert;
			font-weight: 400;
			color: $COLOR_TEXT;
			line-height: 1.27273em;
			font-size: 18px;
			text-align: left;

			@include bp(sm) {
				font-size: 30px;
			}
		}

		.description {
			width: 100%;
			color: $COLOR_BEIGE_DARK;
			line-height: 1.27273em;
			font-size: 14px;

			@include bp(sm) {
				font-size: 16px;
			}
		}

		.modal-documents {
			margin: 25px 0 10px;

			a {
				height: 56px;
				text-decoration: none;
				color: $COLOR_GREY;
				display: flex;
				align-items: center;
				padding: 6px 0;
				line-height: 1.7;
				transition: color 0.3s;
				font-size: 14px;
				font-weight: 300;
				border-bottom: 1px dashed $COLOR_GREY;

				@include bp(sm) {
					font-size: 18px;
				}

				svg {
					width: 12px;
					height: 10px;
					margin-right: 6px;

					g path {
						transition: fill 0.3s;
					}
				}

				span {
					position: relative;
					display: inline-block;
					margin-left: 18px;

					&:after {
						height: 2px;
						width: 2px;
						background-color: $COLOR_GREY;
						content: '';
						position: absolute;
						top: calc(50% - 1px);
						left: -11px;
					}
				}

				@media (hover: hover) {
					&:hover {
						color: $COLOR_TEXT;

						span {
							transform: translateX(3px);
						}

						svg g path {
							fill: $COLOR_RED;
						}
					}
				}
			}
		}
	}
}
